import { computed, defineComponent, onBeforeUnmount, PropType, ref, Ref, watch, useAttrs } from 'vue'
	
export default defineComponent({
	name: "FsAlert",
  model:{
    prop: 'show',
    event: 'change'
  },
	props: {
    /**
    * Value for the 'aria-label' attribute on the dismiss button
    */
    dismissLabel: {
      type: String,
      default: 'Close'
    },
    /**
    * When set, enables the dismiss close button
    */
    dismissible: {
      type: Boolean,
      default: false
    },
    /**
    * When set to `true`, enables the fade animation/transition on the component
    */
    fade: {
      type: Boolean,
      default: false
    },
    /**
    * When set, shows the alert. Set to a number (seconds) to show and automatically dismiss the alert after the number of seconds has elapsed
    */
     show: {   			
      type: [Boolean, Number, String],
      default: false,
    }, 
    modelValue: {
      type: [Boolean, Number, String],
      default: false
    },
    /**
    * Applies one of the Bootstrap theme color variants to the component
    */
    variant: {
      type: String,
      default: 'info'
    }
  },
	emits: ['update:modelValue', 'dismissed', 'dismiss-count-down'],
	setup(props, context) {     
    const attrs = useAttrs()  
    let countDownTimeout: undefined | ReturnType<typeof setTimeout>
    const countDown: Ref<number> = ref(0); 
    
    const computedValue = computed<string>(() => {
      return props.modelValue.toString();
    })

    const computedClasses = computed<any>(() => {
      return ({
        [`alert-${props.variant}`]: props.variant,
        [`show`]: props.modelValue,
        [`alert-dismissible`]: props.dismissible,
        [`fade`]: props.modelValue,
      });
    });

    const isAlertVisible = computed<boolean>(() => {
        return props.modelValue as boolean || props.show as boolean;
    });
     
    onBeforeUnmount(() => {
        clearCountDownInterval();
    });		
		
    watch
    (countDown, (newValue: any) => {
      clearCountDownInterval();
  
      context.emit('dismiss-count-down', newValue)
  
      if (newValue === 0 && props.modelValue > 0) {
        context.emit('dismissed');
      }
  
      if (props.modelValue !== newValue) {
        context.emit('update:modelValue', newValue)
      }
  
      if (newValue > 0) {
        decrement();
      }
    });
  
    watch(() =>
      props.show, () => {
        setCountDown();
      }
    )
		
    watch(() => 
      props.modelValue, () => {
        setCountDown();
      }
    )

    function decrement(){
      countDownTimeout = setTimeout(() => {
        countDown.value--;
      }, 1000)
    }
    
    function dismissClicked(): void {
      if (typeof props.modelValue === 'boolean') {
        context.emit('update:modelValue', false);
      } 
      else {
        context.emit('update:modelValue', 0);
      }
      context.emit('dismissed');
    }
  
    function clearCountDownInterval(): void{
     if (countDownTimeout === undefined)
      {
       return;
      }   
     clearTimeout(countDownTimeout);
     countDownTimeout = undefined;
    }
  
    function toInteger(value: number, defaultValue = NaN): number {
      return Number.isInteger(value) ? value : defaultValue
    }
  
    function setCountDown(): void{
      if (typeof props.modelValue === 'boolean') {
        countDown.value = props.show as number;
        return;
      }
      
      if (typeof props.modelValue === 'number') {
        const numberValue = toInteger(props.modelValue, 0)
        countDown.value = numberValue > 0 ? numberValue : 0
        return;
      }

      if (typeof props.modelValue === 'string'){
        const numberValue = toInteger(Number(props.modelValue), 0)
        return;
      }
    }

    setCountDown();             
 
		return {
      isAlertVisible,
      computedClasses,
      dismissClicked,
      computedValue
		}
	},
})