import { computed, defineComponent, onBeforeMount, PropType, ref, Ref, watch } from 'vue'
	
	export default defineComponent({
		name: "FsFormFile",
      model: {
         prop: 'data',
         event: 'change'
      },
      props: {
         /**
         * Value to set on the file input's `accept` attribute
         */
         accept: {
            type: String,
            default: ''
         },
         /**
         * When set to `true`, attempts to auto-focus the control when it is mounted, or re-activated when in a keep-alive. Does not set the `autofocus` attribute on the control
         */
         autofocus: {
            type: Boolean,
            default: false
         },
         /**
         * Text content for the file browse button
         */
         browseText: {
            type: String,
            default: 'Browse'
         },
         /**
         * When set, will instruction the browser to use the devices camera (if supported)
         */
         capture: {
            type: Boolean,
            default: false
         },
         /**
         * Enable `directory` mode (on browsers that support it)
         */
         directory: {
            type: Boolean,
            default: false
         },
         /**
         * When set to `true`, disables the component's functionality and places it in a disabled state
         */
         disabled: {
            type: Boolean,
            default: false
         },
         /**
         * Text to display as the placeholder when files are being dragged and it's allowed to drop them
         */
         dropPlaceholder: {
            type: String,
            default: 'Drop files here'
         },
         /**
         * Method to format the file names for display. Refer to the docs for details
         */
         fileNameFormatter: {
            type: Function
         },
         /**
         * ID of the form that the form control belongs to. Sets the `form` attribute on the control
         */
         form: {
            type: String
         },
         /**
         * Used to set the `id` attribute on the rendered content, and used as the base to generate any additional element IDs as needed
         */
         id: {
            type: String
         },
         /**
         * When set, will allow multiple files to be selected. `v-model` will be an array
         */
         multiple: {
            type: Boolean,
            default: false
         },
         /**
         * Sets the value of the `name` attribute on the form control
         */
         name: {
            type: String
         },
         /**
         * Disable drag and drop mode
         */
         noDrop: {
            type: Boolean,
            default: false
         },
         /**
         * Text to display as the placeholder when files are being dragged and it isn't allowed to drop them
         */
         noDropPlaceholder: {
            type: String,
            default: 'Not allowed'
         },
         /**
         * Wether to returns files as a flat array when in `directory` mode
         */
         noTraverse: {
            type: Boolean,
            default: false
         },
         /**
         * Sets the `placeholder` attribute value on the form control
         */
         placeholder: {
            type: String,
            default: 'No file chosen'
         },
         /**
         * Render the form control in plain mode, rather than custom styled mode
         */
         plain: {
            type: Boolean,
            default: false
         },
         /**
         * Adds the `required` attribute to the form control
         */
         required: {
            type: Boolean,
            default: false
         },
         /**
         * Set the size of the component's appearance. 'sm', 'md' (default), or 'lg'
         */
         size: {
            type: String
         },
         /**
         * Controls the validation state appearance of the component. `true` for valid, `false` for invalid, or `null` for no validation state
         */
         state: {
            type: Boolean,
            default: null
         },
         /**
         * The current value of the file input. Will be a single `File` object or an array of `File` objects (if `multiple` or `directory` is set). Can be set to `null`, or an empty array to reset the file input
         */
          modelValue: {
            type:  [Array, Object] as PropType<File[] | File>,
         }
      },
      emits: ['update:modelValue'],
      setup(props, context) { 
         const placeholderText = ref("");
         const placeholderTextAux = ref("");    
         const internalId = ref("");

         let dragCounter = 0;

         onBeforeMount(() => {
             if (!props.id) {
                internalId.value = generateQuickId();
             } else {
                internalId.value = `${props.id}__${generateQuickId()}`
             }
             placeholderText.value = props.placeholder.toString();         
         });

         const divComputedClass = computed<any>(() => {
            return ({
               ['custom-file b-form-file']: true,
               [`fs-custom-control-${props.size}`]: props.size,
            })
         });
      
         const inputComputedClass = computed<any>(() => {
            return ({
               ['form-control']: props.plain,
               ['d-none']: !props.plain,
            })
         })

         const labelComputedClass = computed<any>(() => {
            return ({
               ['d-none']: props.plain,
               ['form-control']: !props.plain,
            })
         })
                  
         watch(() => 
            props.modelValue, (newValue: any) => {
               if(newValue)
               {
                  if(props.multiple || newValue.length)
                  {
                     let text = "";       
         
                     for(let i=0;i<newValue.length;i++)
                     {
                        text += `${newValue[i].name}; `
                     }
               
                     placeholderText.value = text;
                  }
                  else 
                  {
                     placeholderText.value = newValue.name;
                  }         
               }
               else
               {
                  placeholderText.value = props.placeholder.toString();
               }   
            },
            {
               deep: true
            }

         );

         function onChange(event: any) {
           const files: any = event.target.files || event.dataTransfer.files;
           
           context.emit('update:modelValue', props.multiple ? <Array<File>>files : <File>files[0]);
         }  
         
         function onFileDrop(event: any)
         {
           if(props.noDrop)
           {
              dragCounter = 0;
              placeholderText.value = placeholderTextAux.value;
              return false; 
           }
             
            onChange(event);
         }   

         function onDragenter(event: Event)
         {
            dragCounter++;

            event.preventDefault();
            event.stopImmediatePropagation();
            
            if(dragCounter === 1)
            {
               placeholderTextAux.value = placeholderText.value;
            }
            
            if(props.noDrop && props.noDrop === true)
            {
               placeholderText.value = props.noDropPlaceholder.toString();
            }
            else
            {
               placeholderText.value = props.dropPlaceholder.toString();
            }
         }

         function onDragleave(event: Event)
         {
            dragCounter--;

            event.preventDefault();
            event.stopImmediatePropagation();
            
            if(dragCounter===0)
            {
               placeholderText.value = placeholderTextAux.value;
            }
         }

         function generateQuickId(): string {
            return 'xxxxxxxx'.replace(/[xy]/g, function (c) {
               const r = Math.random() * 16 | 0,
                  v = c == 'x' ? r : (r & 0x3 | 0x8);
               return v.toString(16);
            });
         }

			return {
            placeholderText,
            placeholderTextAux,
            internalId,
            divComputedClass,
            inputComputedClass,
            labelComputedClass,
            onChange,
            onFileDrop,
            onDragenter,
            onDragleave,
			}
		},
	})