import { computed, defineComponent, PropType } from 'vue'

export default defineComponent({
    name: "FsCardFooter",
    props: {
      /**
      * Text content to place in the footer
      */
      footer: {
        type: String
      },
      /**
      * Applies one of the Bootstrap theme color variants to the footer background
      */
      footerBgVariant: {
        type: String
      },
      /**
      * Applies one of the Bootstrap theme color variants to the footer border
      */
      footerBorderVariant: {
        type: String
      },
      /**
      * CSS class (or classes) to apply to the footer
      */
      footerClass: {
        type: [Array, Object, String] as PropType<[] | Object | String>
      },
      /**
      * HTML string content to place in the footer
      */
      footerHtml: {
        type: String
      },
      /**
      * Specify the HTML tag to render instead of the default tag for the footer
      */
      footerTag: {
        type: String,
        default: 'div'
      },
      /**
      * Applies one of the Bootstrap theme color variants to the footer text
      */
      footerTextVariant: {
        type: String
      }
    },
    setup(props){
      const finalClass = computed<any>(() => {
        return ({
          ['text-' + props.footerTextVariant]: props.footerTextVariant,
          ['bg-' + props.footerBgVariant]: props.footerBgVariant,
          ['border-' + props.footerBorderVariant]: props.footerBorderVariant,
        })
      })

      return {
        finalClass
      };
    }
})