import { defineComponent, onMounted, onBeforeUnmount, PropType, ref, nextTick, ComponentPublicInstance, watch } from 'vue';
//import Tooltip from 'bootstrap/js/dist/tooltip';
import { Tooltip } from 'bootstrap'



export default defineComponent({
    name: "FsTooltip",
    props: {
        /**
        * The boundary constraint of the tooltip: 'scrollParent', 'window', 'viewport', or a reference to an HTMLElement or component
        */
        boundary: {
            type: [HTMLElement, Object, String] as PropType<HTMLElement | Object | String>,
            default: 'scrollParent'
        },
        /**
        * The tooltip will try and stay away from the edge of the boundary element by the number of pixels specificed
        */
        boundaryPadding: {
            type: [Number, String] as PropType<Number | String>,
            default: 50
        },
        /**
        * The container element to append the rendered tooltip when visible. Default's to the body element
        */
        container: {
            type: [HTMLElement, Object, String] as PropType<HTMLElement | Object | String>
        },
        /**
        * CSS class (or classes) to apply to the tooltip's root element
        */
        customClass: {
            type: String
        },
        /**
        * Value for the show and hide delay. Applies to both show and hide when specified as a number or string. Use object form to set show and hide delays individually
        */
        delay: {
            type: [Number, Object, String] as PropType<Number | Object | String>,
            default: 50
        },
        /**
        * When set to `true`, disables the component's functionality and places it in a disabled state
        */
        disabled: {
            type: Boolean,
            default: false
        },
        /**
        * Placement to use when the tooltip would be out of boundaries. Refer to the docs for more details
        */
        fallbackPlacement: {
            type: [Array, String] as PropType<[] | String>,
            default: 'flip'
        },
        /**
        * Used to set the `id` attribute on the rendered content, and used as the base to generate any additional element IDs as needed
        */
        id: {
            type: String
        },
        /**
        * When set to `true`, disables the fade animation/transition on the component
        */
        noFade: {
            type: Boolean,
            default: false
        },
        /**
        * Wether the tooltip should not be user-interactive
        */
        noninteractive: {
            type: Boolean,
            default: false
        },
        /**
        * Offset (in pixels) for the arrow center compared to the trigger target element
        */
        offset: {
            type: [Number, String] as PropType<Number | String>,
            default: 0
        },
        /**
        * Placement of the tooltip: One of 'top', 'bottom', 'right', 'left', 'topleft', 'topright', 'bottomleft', 'bottomright', 'lefttop', 'leftbottom', 'righttop', 'rightbottom'
        */
        placement: {
            type: String,
            default: 'top'
        },
        /**
        * When set will show the tooltip
        */
        show: {
            type: Boolean,
            default: false
        },
        /**
        * Element string ID, or a reference to an element or component, that you want to trigger the tooltip
        */
        target: {
            type: [HTMLElement, SVGElement, Function, Object, String] as PropType<HTMLElement | SVGElement | Function | Object | String>
        },
        /**
        * Text to place in the tooltip
        */
        title: {
            type: String
        },
        /**
        * Specify which triggers will show the tooltip. Supported values are 'click', 'hover', 'focus'. Refer to the docs for special triggers 'blur' and 'manual'
        */
        triggers: {
            type: [Array, String] as PropType<[] | String>,
            default: 'hover focus'
        },
        /**
        * Applies one of the Bootstrap theme color variants to the component
        */
        variant: {
            type: String
        },
    },

    emits: ['show', 'shown', 'hide', 'hidden', 'inserted'],

    setup(props, {emit}) {
        const targetElement = ref<HTMLElement | undefined>();
        const tooltipInstance = ref<Tooltip>();
        const elementRef = ref<HTMLElement>();
        const titleRef = ref<HTMLElement>();

        const getTargetElement = (): HTMLElement | undefined => {
            if (typeof props.target === 'string') {
                return document.getElementById(props.target.replace(/^#/, '')) || null;
            }
            if (props.target instanceof HTMLElement) {
                return props.target;
            }
            if (typeof props.target === 'function') {
                let result = props.target();
    
                if (result && (result).$el instanceof HTMLElement)
                {
                    return result.$el;
                }
            }
            if (typeof props.target !== 'undefined') {
                return (props.target as ComponentPublicInstance<HTMLElement>).$el as HTMLElement;
            }

            return null;
        }

        const generateTooltipInstance = (): void => {
            targetElement.value = getTargetElement();
             if (!targetElement) {
                return;
            }

            const options = {
                container: props.container ? props.container : false,
                delay: props.delay,
                html: true,
                placement: props.placement,
                title: props.title ? props.title : titleRef.value,
                trigger: props.triggers,
                offset: props.offset.toString(),
                fallbackPlacement: props.fallbackPlacement,
                boundary: props.boundary,
            };

             tooltipInstance.value = new Tooltip(targetElement.value, options);

            targetElement.value?.addEventListener('show.bs.tooltip', (e: Event) => emit('show', e));
            targetElement.value?.addEventListener('shown.bs.tooltip', (e: Event) => emit('shown', e));
            targetElement.value?.addEventListener('hide.bs.tooltip', (e: Event) => emit('hide', e));
            targetElement.value?.addEventListener('hidden.bs.tooltip', (e: Event) => emit('hidden', e));
            targetElement.value?.addEventListener('inserted.bs.tooltip', (e: Event) => emit('inserted', e));
        }

        watch (
            () => props.target,
            () => {
                tooltipInstance.value?.tooltip('dispose');
                generateTooltipInstance();
            }
        );

        onMounted(() => {
            nextTick(() => {
                elementRef.value?.parentNode?.removeChild(elementRef.value);
                setTimeout(() => {
                    generateTooltipInstance();
                }, 50);
            });
        });

        onBeforeUnmount(() => {
            tooltipInstance.value?.dispose();

            targetElement.value?.removeEventListener('show.bs.tooltip', (e: Event) => emit('show', e));
            targetElement.value?.removeEventListener('shown.bs.tooltip', (e: Event) => emit('shown', e));
            targetElement.value?.removeEventListener('hide.bs.tooltip', (e: Event) => emit('hide', e));
            targetElement.value?.removeEventListener('hidden.bs.tooltip', (e: Event) => emit('hidden', e));
            targetElement.value?.removeEventListener('inserted.bs.tooltip', (e: Event) => emit('inserted', e));
        });

        return {
            elementRef,
            titleRef
        }

    }

})