import { computed, defineComponent } from 'vue'

export default defineComponent({
    name: "FsSpinner",
    props: {
        /**
        * Text content to place in the visually-hidden-focusable label
        */
        label: {
            type: String
        },
        /**
        * Sets the ARIA attribute `role` to a specific value
        */
        role: {
            type: String,
            default: 'status'
        },
        /**
        * When set, rendered a smaller spinner suitable for placing in buttons
        */
        small: {
            type: Boolean,
            default: false
        },
        /**
        * Specify the HTML tag to render instead of the default tag
        */
        tag: {
            type: String,
            default: 'span'
        },
        /**
        * Type of spinner to show. Current supported types are 'border' and 'grow'
        */
        type: {
            type: String,
            default: 'border'
        },
        /**
        * Applies one of the Bootstrap theme color variants to the component
        */
        variant: {
            type: String
        },
    },
    setup(props) {
        const computedClass = computed<any>(() => {
            return ({
                [`spinner-${props.type}`]: props.type,
                [`spinner-${props.type}-sm`]: props.small,
                [`text-${props.variant}`]: props.variant
            })
        });

        return {
            computedClass
        };
    },
})