import { computed, defineComponent } from 'vue';

export default defineComponent({
    name: "FsNav",
    props: {
        /**
        * Align the nav items in the nav: 'start' (or 'left'), 'center', 'end' (or 'right')
        */
        align: {
            type: String
        },
        /**
        * Set this prop when the nav is placed inside a card header
        */
        cardHeader: {
            type: Boolean,
            default: false
        },
        /**
        * Proportionately fills all horizontal space with nav items. All horizontal space is occupied, but not every nav item has the same width
        */
        fill: {
            type: Boolean,
            default: false
        },
        /**
        * Fills all horizontal space with nav items, but unlike 'fill', every nav item will be the same width
        */
        justified: {
            type: Boolean,
            default: false
        },
        /**
        * Renders the nav items with the appearance of pill buttons
        */
        pills: {
            type: Boolean,
            default: false
        },
        /**
        * Makes the nav smaller
        */
        small: {
            type: Boolean,
            default: false
        },
        /**
        * Renders the nav items with the appearance of tabs
        */
        tabs: {
            type: Boolean,
            default: false
        },
        /**
        * Specify the HTML tag to render instead of the default tag
        */
        tag: {
            type: String,
            default: 'ul'
        },
        /**
        * Renders the nav vertically
        */
        vertical: {
            type: Boolean,
            default: false
        },
    },
    setup(props){
        function computeJustifyContent(value: any){
            value = value === 'left' ? 'start' : value === 'right' ? 'end' : value
            return `justify-content-${value}`
        }
        const finalClass = computed<any>(() => {
            return ({
                'nav-tabs': props.tabs,
                'nav-pills': props.pills && !props.tabs,
                'card-header-tabs': !props.vertical && props.cardHeader && props.tabs,
                'card-header-pills': !props.vertical && props.cardHeader && props.pills && !props.tabs,
                'flex-column': props.vertical,
                'nav-fill': !props.vertical && props.fill,
                'nav-justified': !props.vertical && props.justified,
                [computeJustifyContent(props.align)]: !props.vertical && props.align,
                small: props.small
            })
        });

        return {
            finalClass,
        }
    }
})
