import { computed, defineComponent, PropType } from 'vue'
import FsCardTitle from '../fsCardTitle/fsCardTitle.vue'
import FsCardSubTitle from '../fsCardSubTitle/fsCardSubTitle.vue'

export default defineComponent({
    name: "FsCardBody",
    props: {
      /**
      * Applies one of the Bootstrap theme color variants to the body background
      */
      bodyBgVariant: {
        type: String
      },
      /**
      * Applies one of the Bootstrap theme color variants to the body border
      */
      bodyBorderVariant: {
        type: String
      },
      /**
      * CSS class (or classes) to apply to the body
      */
      bodyClass: {
        type: [Array, Object, String] as PropType<[] | Object | String>
      },
      /**
      * Specify the HTML tag to render instead of the default tag for the body
      */
      bodyTag: {
        type: String,
        default: 'div'
      },
      /**
      * Applies one of the Bootstrap theme color variants to the body text
      */
      bodyTextVariant: {
        type: String
      },
      /**
      * When set, will overlay the card body on top of the image (if the card has an image)
      */
      overlay: {
        type: Boolean,
        default: false
      },
      /**
      * Text content to place in the sub title
      */
      subTitle: {
        type: String
      },
      /**
      * Specify the HTML tag to render instead of the default tag for the sub title
      */
      subTitleTag: {
        type: String,
        default: 'h6'
      },
      /**
      * Applies one of the Bootstrap theme color variants to the sub title text
      */
      subTitleTextVariant: {
        type: String,
        default: 'muted'
      },
      /**
      * Text content to place in the title
      */
      title: {
        type: String
      },
      /**
      * Specify the HTML tag to render instead of the default tag for the title
      */
      titleTag: {
        type: String,
        default: 'h4'
      }
    },
    components: {
      FsCardTitle,
      FsCardSubTitle,
    },
    setup(props){
      const finalClass = computed<any>(() => {
        return ({
          ['text-' + props.bodyTextVariant]: props.bodyTextVariant,
          ['bg-' + props.bodyBgVariant]: props.bodyBgVariant,
        })
      })

      return {
        finalClass
      };
    }
})