import FsTooltip from "./components/fsTooltip/fsTooltip.vue";

import VFSModal from "./directives/FsModal";
import VFSPopover from "./directives/FsPopover";
import VFSTooltip from "./directives/FsTooltip";
//import VFSScrollspy from "./directives/scrollspy";
import VFSToggle from "./directives/FsToggle";

function FsBootstrapPluginFunction(Vue: App, options?: any): void {
    Vue.directive("FsModal", VFSModal);
    Vue.directive("FsPopover", VFSPopover);
    //Vue.directive("FsScrollspy",VFSScrollspy);
    Vue.directive("FsTooltip", VFSTooltip);
    Vue.directive("FsToggle", VFSToggle);

    Vue.component("FsAccordion", FsAccordion as any);
    Vue.component("FsAccordionItem", FsAccordionItem as any);
    Vue.component("FsAlert", FsAlert as any);
    Vue.component("FsAspect", FsAspect as any);
    Vue.component("FsBadge", FsBadge as any);
    Vue.component("FsButton", FsButton as any);
    Vue.component("FsButtonGroup", FsButtonGroup as any);
    Vue.component("FsCard", FsCard as any);
    Vue.component("FsCardBody", FsCardBody as any);
    Vue.component("FsCardFooter", FsCardFooter as any);
    Vue.component("FsCardGroup", FsCardGroup as any);
    Vue.component("FsCardHeader", FsCardHeader as any);
    Vue.component("FsCardImg", FsCardImg as any);
    Vue.component("FsCardSubTitle", FsCardSubTitle as any);
    Vue.component("FsCardText", FsCardText as any);
    Vue.component("FsCardTitle", FsCardTitle as any);
    Vue.component("FsCol", FsCol as any);
    Vue.component("FsCollapse", FsCollapse as any);
    Vue.component("FsContainer", FsContainer as any);
    Vue.component("FsDropdown", FsDropdown as any);
    Vue.component("FsDropdownDivider", FsDropdownDivider as any);
    Vue.component("FsDropdownForm", FsDropdownForm as any);
    Vue.component("FsDropdownGroup", FsDropdownGroup as any);
    Vue.component("FsDropdownHeader", FsDropdownHeader as any);
    Vue.component("FsDropdownItem", FsDropdownItem as any);
    Vue.component("FsDropdownItemButton", FsDropdownItemButton as any);
    Vue.component("FsDropdownText", FsDropdownText as any);
    Vue.component("FsFormCheckbox", FsFormCheckbox as any);
    Vue.component("FsFormCheckboxGroup", FsFormCheckboxGroup as any);
    Vue.component("FsFormFile", FsFormFile as any);
    Vue.component("FsFormGroup", FsFormGroup as any);
    Vue.component("FsFormInput", FsFormInput as any);
    Vue.component("FsFormRadio", FsFormRadio as any);
    Vue.component("FsFormRadioGroup", FsFormRadioGroup as any);
    Vue.component("FsFormSelect", FsFormSelect as any);
    Vue.component("FsFormSelectOption", FsFormSelectOption as any);
    Vue.component("FsFormSelectOptionGroup", FsFormSelectOptionGroup as any);
    Vue.component("FsFormTextarea", FsFormTextarea as any);
    Vue.component("FsImg", FsImg as any);
    Vue.component("FsInputGroup", FsInputGroup as any);
    Vue.component("FsInputGroupText", FsInputGroupText as any);
    Vue.component("FsLink", FsLink as any);
    Vue.component("FsListGroup", FsListGroup as any);
    Vue.component("FsListGroupItem", FsListGroupItem as any);
    Vue.component("FsModal", FsModal as any);
    Vue.component("FsNav", FsNav as any);
    Vue.component("FsNavItem", FsNavItem as any);
    Vue.component("FsNavForm", FsNavForm as any);
    Vue.component("FsNavItemDropdown", FsNavItemDropdown as any);
    Vue.component("FsNavText", FsNavText as any);
    Vue.component("FsNavbar", FsNavbar as any);
    Vue.component("FsNavbarBrand", FsNavbarBrand as any);
    Vue.component("FsNavbarNav", FsNavbarNav as any);
    Vue.component("FsNavbarToggle", FsNavbarToggle as any);
    Vue.component("FsOverlay", FsOverlay as any);
    Vue.component("FsPagination", FsPagination as any);
    Vue.component("FsPopover", FsPopover as any);
    Vue.component("FsPopper", FsPopper as any);
    Vue.component("FsProgress", FsProgress as any);
    Vue.component("FsProgressBar", FsProgressBar as any);
    Vue.component("FsRouterLink", FsRouterLink as any);
    Vue.component("FsRow", FsRow as any);
    Vue.component("FsSidebar", FsSidebar as any);
    Vue.component("FsSpinner", FsSpinner as any);
    Vue.component("FsTable", FsTable as any);
    Vue.component("FsTableSimple", FsTableSimple as any);
    Vue.component("FsTh", FsTh as any);
    Vue.component("FsTr", FsTr as any);
    Vue.component("FsTd", FsTd as any);
    Vue.component("FsThead", FsThead as any);
    Vue.component("FsTbody", FsTbody as any);
    Vue.component("FsTfoot", FsTfoot as any);
    Vue.component("FsTab", FsTab as any);
    Vue.component("FsTabs", FsTabs as any);
    Vue.component("FsTooltip", FsTooltip as any);
}

const FsBootstrapPlugin = {
    install: FsBootstrapPluginFunction
}

export default FsBootstrapPlugin;