import { computed, defineComponent, PropType } from 'vue'

export default defineComponent({
    name: "FsCol",
    props: {
      /**
      * Vertical alignment of the grid cell with respect to the row: 'start', 'center', 'end', 'baseline', or 'stretch'
      */
      alignSelf: {
        type: String,
        default: null
      },
      /**
      * When true makes an equal width column grid cell spans for xs and up breakpoints
      */
      col: {
        type: Boolean,
        default: false
      },
      /**
      * Number of columns the grid cell spans for xs and up breakpoints
      */
      cols: {
        type: [Number, String] as PropType<Number | String>
      },
      /**
      * Number of columns the grid cell spans for lg and up breakpoints
      */
      lg: {
        type: [Boolean, Number, String] as PropType<Boolean | Number | String>
      },
      /**
      * Number of columns the grid cell spans for md and up breakpoints
      */
      md: {
        type: [Boolean, Number, String] as PropType<Boolean | Number | String>
      },
      /**
      * Number of columns the grid cell is offset for xs and up breakpoints
      */
      offset: {
        type: [Number, String] as PropType<Number | String>
      },
      /**
      * Number of columns the grid cell is offset for lg and up breakpoints
      */
      offsetLg: {
        type: [Number, String] as PropType<Number | String>
      },
      /**
      * Number of columns the grid cell is offset for md and up breakpoints
      */
      offsetMd: {
        type: [Number, String] as PropType<Number | String>
      },
      /**
      * Number of columns the grid cell is offset for sm and up breakpoints
      */
      offsetSm: {
        type: [Number, String] as PropType<Number | String>
      },
      /**
      * Number of columns the grid cell is offset for xl and up breakpoints
      */
      offsetXl: {
        type: [Number, String] as PropType<Number | String>
      },
      /**
      * Flex order of the grid cell for xs and up breakpoints
      */
      order: {
        type: [Number, String] as PropType<Number | String>
      },
      /**
      * Flex order of the grid cell for lg and up breakpoints
      */
      orderLg: {
        type: [Number, String] as PropType<Number | String>
      },
      /**
      * Flex order of the grid cell for md and up breakpoints
      */
      orderMd: {
        type: [Number, String] as PropType<Number | String>
      },
      /**
      * Flex order of the grid cell for sm and up breakpoints
      */
      orderSm: {
        type: [Number, String] as PropType<Number | String>
      },
      /**
      * Flex order of the grid cell for xl and up breakpoints
      */
      orderXl: {
        type: [Number, String] as PropType<Number | String>
      },
      /**
      * Number of columns the grid cell spans for sm and up breakpoints
      */
      sm: {
        type: [Boolean, Number, String] as PropType<Boolean | Number | String>
      },
      /**
      * Specify the HTML tag to render instead of the default tag
      */
      tag: {
        type: String,
        default: 'div'
      },
      /**
      * Number of columns the grid cell spans for xl and up breakpoints
      */
      xl: {
        type: [Boolean, Number, String] as PropType<Boolean | Number | String>
      }
    },
    emits: [],
    setup(props){
      const hasColBreakpoint = computed<boolean> (() => {
        return !!(props.cols || props.sm || props.md || props.lg || props.lg)
      });

      const computedClasses = computed<any>(() => {
        return ({
          'col': !hasColBreakpoint.value || props.col,
          [`col-${props.cols}`]: !!props.cols && typeof (props.cols) !== 'boolean',
          [`col-sm-${props.sm}`]: !!props.sm && typeof (props.sm) !== 'boolean',
          [`col-md-${props.md}`]: !!props.md && typeof (props.md) !== 'boolean',
          [`col-lg-${props.lg}`]: !!props.lg && typeof (props.lg) !== 'boolean',
          [`col-xl-${props.xl}`]: !!props.xl && typeof (props.xl) !== 'boolean',
          'col-sm': !!props.sm && typeof (props.sm) === 'boolean',
          'col-md': !!props.md && typeof (props.md) === 'boolean',
          'col-lg': !!props.lg && typeof (props.lg) === 'boolean',
          'col-xl': !!props.xl && typeof (props.xl) === 'boolean',
          [`offset-${props.offset}`]: !!props.offset,
          [`offset-sm-${props.offsetSm}`]: !!props.offsetSm,
          [`offset-md-${props.offsetMd}`]: !!props.offsetMd,
          [`offset-lg-${props.offsetLg}`]: !!props.offsetLg,
          [`offset-xl-${props.offsetXl}`]: !!props.offsetXl,
          [`order-${props.order}`]: !!props.order,
          [`order-sm-${props.orderSm}`]: !!props.orderSm,
          [`order-md-${props.orderMd}`]: !!props.orderMd,
          [`order-lg-${props.orderLg}`]: !!props.orderLg,
          [`order-xl-${props.orderXl}`]: !!props.orderXl,
          [`align-self-${props.alignSelf}`]: !!props.alignSelf,
        })
      });

      return {
        computedClasses
      };
      
    }
})