import { ref } from "vue";
const directiveBus = ref(new Map());

export default function useDirectiveBus(){
    function emitToComponentById(id, ...args) {
        directiveBus.value.set(id, args);
    }

    function removeComponentFromBusById(id) {
        directiveBus.value.delete(id);
    }

    return {
        directiveBus, 
        emitToComponentById,
        removeComponentFromBusById
    }
}