import { defineComponent, PropType } from 'vue';
import FsNav from '../../fsNav/fsNav.vue';

export default defineComponent({
  name: "FsNavbarToggle",
  props: {
    /**
    * When `true`, disables the navbar toggle button, and adds class `disabled` to the button
    */
    disabled: {
      type: Boolean,
      default: false
    },
    /**
    * String to place in the toggle's 'aria-label' attribute
    */
    label: {
      type: String as PropType<string>,
      default: 'Toggle navigation'
    },
    /**
    * ID (or array of IDs) of the collapse/sidebar components that should be toggled
    */
    target: {
      type: String as PropType<string>
    },
  }, 
  components: {
    FsNav
  },
  emits: ['click'],
  setup(props, { emit }){
    function clicked(e: PointerEvent) : void{
      if (props.disabled) {
        e.preventDefault();
        e.stopPropagation();
        return
      }
      emit('click', e);
    }

    return {
      clicked
    }
  }
})
