import { computed, defineComponent } from 'vue'

export default defineComponent({
    name: "FsButtonGroup",
    props: {
        /**
        * Sets the ARIA attribute `role` to a specific value
        */
        ariaRole: {
            type: String,
            default: 'group'
        },
        /**
        * Set the size of the component's appearance. 'sm', 'md' (default), or 'lg'
        */
        size: {
            type: String
        },
        /**
        * Specify the HTML tag to render instead of the default tag
        */
        tag: {
            type: String,
            default: 'div'
        },
        /**
        * When set, rendered the button group in vertical mode
        */
        vertical: {
            type: Boolean,
            default: false
        }
    },
    emits: [],
    setup(props){
        const computedClass = computed<any>(() => {
            return ({
                ['btn-group']: !props.vertical,
                [`btn-group-${props.size}`]: props.size,
                [`btn-group-vertical`]: props.vertical,
            });
        });

        return {
            computedClass
        }
    }
})