import { computed, defineComponent, PropType } from 'vue';

export default defineComponent({
    name: "FsNavbar",
    props: {
        /**
        * Set to 'top' for fixed to the top of the viewport, or 'bottom' for fixed to the bottom of the viewport
        */
        fixed: {
            type: String
        },
        /**
        * Navbars are hidden by default when printing. When this prop is set it will be printed
        */
        print: {
            type: Boolean,
            default: false
        },
        /**
        * Set to true to make the navbar stick to the top of the viewport (or parent container that has 'position: relative' set) when scrolled
        */
        sticky: {
            type: Boolean,
            default: false
        },
        /**
        * Specify the HTML tag to render instead of the default tag
        */
        tag: {
            type: String,
            default: 'nav'
        },
        /**
        * Set to 'true' for an always collapsed navbar, or to a specific breakpoint at which point the navbar will be expanded: 'sm', 'md', 'lg' or 'xl'
        */
        toggleable: {
            type: [Boolean, String] as PropType<Boolean | String>,
            default: false
        },
        /**
        * Control the text color by setting to 'light' for use with light background color variants, or 'dark' for dark background color variants
        */
        type: {
            type: String,
            default: 'light'
        },
        /**
        * Applies one of the Bootstrap theme color variants to the component
        */
        variant: {
            type: String
        }
    },
    emits: [],
    setup(props){
        const finalClass = computed<any>(() => {
            return ({
                [`navbar-${props.type}`]: props.type,
                [`bg-${props.variant}`]: props.variant,
                [`bg-${props.variant}`]: props.variant,
                [`fixed-${props.fixed}`]: props.fixed,
                [`navbar-expand-${props.toggleable}`]: props.toggleable && typeof(props.toggleable) !== 'boolean',
                'navbar-expand': !props.toggleable,
            })
        });
        return {
            finalClass
        }
    },
});
