import { ComponentPublicInstance, getCurrentInstance, ref, Ref } from 'vue';
import type { TableField, TableFieldObject, TableItem } from '../types';
import { isObject, isString, startCase } from '../utils';
import { isFunction } from '../utils/inspect';
import { clone } from '../utils/object';
import { cloneDeep } from '../utils/clone-deep';

const useTableItemHelper = () => {
    const internalItems = ref<TableItem[]>([])

    const normaliseFields = (origFields: TableField[], items: TableItem[]): TableFieldObject[] => {
        let fields: TableFieldObject[] = [];
        let calculatedFields: any = {};
        const instance = getCurrentInstance();
        if (!origFields?.length && items?.length) {
            Object.keys(items[0]).forEach((k) => fields.push({ key: k, label: startCase(k) }));
            return fields;
        }

        if (Array.isArray(origFields)) {
            origFields.forEach((f) => {
                if (typeof f === 'string') {
                    fields.push({ key: f, label: startCase(f) });
                } else if (isObject(f) && f.key && isString(f.key)) {
                    fields.push({ ...f });
                }
            });
            calculatedFields = fields.reduce((obj, f) => {
                obj[f.key] = clone(f);
                if (f.formatter) {
                    let formatter = f.formatter
                    if (isString(formatter) && isFunction(instance.proxy.$parent[formatter as string])) {
                        formatter = instance.proxy.$parent[formatter as string];
                    } else if (!isFunction(formatter)) {
                        formatter = undefined;
                    }
                    obj[f.key].formatter = formatter;
                }
                return obj;
            }, {});
        };
        return calculatedFields;
    };

    const mapItems = (
        fields: TableField[],
        items: TableItem[],
        props: any,
        flags: Record<string, Ref<boolean>>,
        localSortBy: string,
        localSortDesc: boolean
      ): TableItem[] => {
        if ('isFilterableTable' in flags && flags.isFilterableTable.value === true && props.filter) {
          items = filterItems(items, fields, props.filter, props.filterIncludedFields)
        }
        if ('isSortable' in flags && flags.isSortable.value === true) {
          items = sortItems(
            fields,
            items,
            {
              key: props.sortBy ? props.sortBy : localSortBy,
              desc: props.sortBy ? flags.sortDescBoolean.value : localSortDesc,
            },
            props.sortCompare
          )
        }
    
        return items
      }

      const realVal = (ob: any) => {
        if (ob === null) {
          return ' '
        } else if (typeof ob === 'number') {
          return ob.toString()
        } else if (typeof ob === 'object') {
          return JSON.stringify(ob)
        } else {
          return ob
        }
      }  
      
      const sortItems = (
        fields: TableField[],
        items: TableItem<Record<string, any>>[],
        sort: {key: string; desc: boolean},
        sorter?: Function
      ) => {
        if (!sort || !sort.key) return items
        const sortKey = sort.key
        return items.sort((a, b) => {
          let result = null;
          if (sorter !== undefined) {
            result = sorter(a, b, sort.key, sort.desc)
          }
          if(result){
            return result;
          }          

          if(sortKey.includes('.')){
            const sortKeys = sortKey.split('.');

            const aHigher = realVal(a[sortKeys[0]][sortKeys[1]]).localeCompare(realVal(b[sortKeys[0]][sortKeys[1]]), 'en', { numeric: true });           
            if (aHigher === 1) {
              return sort.desc ? -1 : 1
            }
            
            const bHigher = realVal(b[sortKeys[0]][sortKeys[1]]).localeCompare(realVal(a[sortKeys[0]][sortKeys[1]]), 'en', { numeric: true });
            if (bHigher === 1) {
              return sort.desc ? 1 : -1
            }
 
             return 0
          };

          const aHigher = realVal(a[sortKey]).localeCompare(realVal(b[sortKey]), 'en', { numeric: true });          
          if (aHigher === 1) {
            return sort.desc ? -1 : 1
          }
           
          const bHigher = realVal(b[sortKey]).localeCompare(realVal(a[sortKey]), 'en', { numeric: true });
          if (bHigher === 1) {
            return sort.desc ? 1 : -1
          }

          return 0
        })
      }
    
      const filterItems = (
        items: TableItem<Record<string, any>>[],
        fields: TableField[],
        filter: string,
        filterIncludedFields: string[]
      ) =>  {
        return items.filter(
          (item) => {
            let filteredItems = Object.entries(item).filter((item) => {
              let [key, val] = item
              if (!val || key[0] === '_'){
                return false
              }
                
              const field = fields.find((field: TableFieldObject) => field.key === key);
              if((field as TableFieldObject)?.formatter){
                val = ((field as TableFieldObject).formatter as any)(val, key, item)
              }

              const itemValue: string =
                typeof val === 'object'
                  ? JSON.stringify(Object.values(val))
                  : typeof val === 'string'
                  ? val
                  : val.toString()

              if(filterIncludedFields){
                if(filterIncludedFields.includes(key)){
                  return itemValue.toLowerCase().includes(filter.toLowerCase())
                }
              } else {
                  return itemValue.toLowerCase().includes(filter.toLowerCase())
              }
            })

            return filteredItems.length > 0;
          })
        };

    return {
        normaliseFields,
        mapItems
    };
}


export default useTableItemHelper;