import { isArray, isPlainObject, isString, isUndefinedOrNull } from './inspect';

// String utilities
import { RX_HYPHENATE, RX_HYPHEN, RX_UNDERSCORE, RX_FIRST_LOWER } from '../constants/regex';

// Converts PascalCase or camelCase to kebab-case
export const kebabCase: (str: any) => any = (str) => {
    return str.replace(RX_HYPHENATE, '-$1').toLowerCase();
};


// C
export const spaceCase: (str: string) => string = (str) => {
    return str.replace(RX_UNDERSCORE, ' ')
              .replace(RX_FIRST_LOWER, (w) => w.charAt(0).toUpperCase() + w.slice(1))
}

// Convert a value to a string that can be rendered
// `undefined`/`null` will be converted to `''`
// Plain objects and arrays will be JSON stringified
export const toString = (val: { toString: () => string; }, spaces = 2) => {
    return isUndefinedOrNull(val) ? '' : isArray(val) || (isPlainObject(val) && val.toString === Object.prototype.toString) ? JSON.stringify(val, null, spaces) : String(val)
}

// Remove leading and trailing white space from a string
export const trim = str => toString(str).trim()
