import { computed, defineComponent, PropType } from 'vue'

export default defineComponent({
    name: "FsDropdownText",
    props: {
        /**
        * Specify the HTML tag to render instead of the default tag
        */
        tag: {
            type: String,
            default: 'p'
        },
        /**
        * Class or classes to apply to the inner element
        */
        textClass: {
            type: [Array, Object, String] as PropType<[] | Object | String>
        },
        /**
        * Applies one of the Bootstrap theme color variants to the component
        */
        variant: {
            type: String
        }
    },
    setup(props){
        const finalClass = computed<any>(() => {
            return ({
                [`text-${props.variant}`]: props.variant,
            })
        });

        return {
          finalClass
        };
        
    }
})