import { computed, defineComponent } from 'vue';
import fsInputGroupText from './fsInputGroupText/fsInputGroupText.vue';

export default defineComponent({
  name: "FsInputGroup",
	props: {
    /**
    * Text to append to the input group
    */
    append: {
      type: String
    },
    /**
    * HTML string to append to the input group. Has precedence over 'append' prop
    */
    appendHtml: {
      type: String
    },
    /**
    * Used to set the `id` attribute on the rendered content, and used as the base to generate any additional element IDs as needed
    */
    id: {
      type: String
    },
    /**
    * Text to prepend to the input group
    */
    prepend: {
      type: String
    },
    /**
    * HTML string to prepend to the input group. Has precedence over 'prepend' prop
    */
    prependHtml: {
      type: String
    },
    /**
    * Set the size of the component's appearance. 'sm', 'md' (default), or 'lg'
    */
    size: {
      type: String
    },
    /**
    * Specify the HTML tag to render instead of the default tag
    */
    tag: {
      type: String,
      default: 'div'
    },
  },
  components: {
    fsInputGroupText
  },
  emits: [],
  setup(props, { slots }) {
    const computedClass = computed<any>(() => {
      return ({
        'input-group-sm' : props.size === 'sm',
        'input-group-lg' : props.size === 'lg',
      });
    });
    const hasAppend = computed<boolean>(() => {
      return !!(props.append || props.appendHtml);
    });
    const hasPrepend = computed<boolean>(() => {
      return !!(props.prepend || props.prependHtml);
    });
    const showAppendHtml = computed<boolean>(() => {
      return !!(props.appendHtml);
    });
    const showPrependHtml = computed<boolean>(() => {
      return !!(props.prependHtml);
    });
    const hasAppendSlot = computed<boolean>(() => {
      return !!slots.append;
    });
    const hasPrependSlot = computed<boolean>(() => {
      return !!slots.prepend;
    });

    return {
      computedClass,
      hasAppend,
      hasPrepend,
      showAppendHtml,
      showPrependHtml,
      hasAppendSlot,
      hasPrependSlot
    }
  }
})
