import { defineComponent, PropType, computed, } from 'vue'
import { normalizeOptions } from './fsFormSelectHelper'
import FsFormSelectOption from './fsFormSelectOption';

export default defineComponent({
    name: "FsFormSelectOptionGroup",
    props: {
        /**
        * The label for the option group
        */
        label: {
            type: String,
            required: true,
        },
        /**
       * Field name in the `options` array that should be used for the disabled state
       */
        disabledField: {
            type: String,
            default: 'disabled'
        },
        /**
         * Field name in the `options` array that should be used for the html label instead of text field
         */
        htmlField: {
            type: String,
            default: 'html'
        },
        /**
         * Array of items to render in the component
         */
        options: {
            type: [Array, Object],
            default: () => []
        },
        /**
         * Field name in the `options` array that should be used for the text label
         */
        textField: {
            type: String,
            default: 'text'
        },

        /**
         * Field name in the `options` array that should be used for the value
         */
        valueField: {
            type: String,
            default: 'value'
        }
    },
    emits: [],
    setup(props) {
        const formOptions = computed<any>(() => {
            return normalizeOptions(props.options as any, 'fsFormSelectOptionGroup', props)
        });


        return {
            formOptions,
        }
    },
    components: {
        FsFormSelectOption
    }
})