import { computed, defineComponent, PropType } from 'vue';
import FsSpinner from '../fsSpinner/fsSpinner.vue';

export default defineComponent({
    name: "FsOverlay",
	props: {
		/**
		* CSS color to use as the opaque overlay backdrop color. If set, overrides the `variant` prop
		*/
		bgColor: {
			type: String
		},
		/**
		* Value for the CSS blur backdrop-filter. Be sure to include the CSS units. Not supported in IE 11. Set to null or an empty string to disable blurring
		*/
		blur: {
			type: String,
			default: '2px'
		},
		/**
		* When prop `no-wrap` is set, will use fixed positioning instead of absolute positioning. Handy if you want to obscure the entire application page
		*/
		fixed: {
			type: Boolean,
			default: false
		},
		/**
		* When set, disables the vertical and horizontal centering of the overlay content
		*/
		noCenter: {
			type: Boolean,
			default: false
		},
		/**
		* Disables the fade transition of the overlay
		*/
		noFade: {
			type: Boolean,
			default: false
		},
		/**
		* Disabled generating the wrapper element, and ignored the default slot. Requires that `<b-overlay>` be placed in an element with position relative set
		*/
		noWrap: {
			type: Boolean,
			default: false
		},
		/**
		* Opacity of the overlay backdrop. Valid range is `0` to `1`
		*/
		opacity: {
			type: [Number, String] as PropType<Number | String>,
			default: 0.85
		},
		/**
		* Element tag to use as for the overlay element
		*/
		overlayTag: {
			type: String,
			default: 'div'
		},
		/**
		* Apply rounding to the overlay to match your content routing. Valid values are `true`, `'sm'`, `lg`, `circle`, `pill`, `top`, `right`, `bottom`, or `left`
		*/
		rounded: {
			type: [Boolean, String] as PropType<Boolean | String>,
			default: false
		},
		/**
		* When set, shows the overlay
		*/
		show: {
			type: Boolean,
			default: false
		},
		/**
		* When set, renders the default spinner in a smaller size
		*/
		spinnerSmall: {
			type: Boolean,
			default: false
		},
		/**
		* Type of the default spinner to show. Current supported types are 'border' and 'grow'
		*/
		spinnerType: {
			type: String,
			default: 'border'
		},
		/**
		* Applies one of the Bootstrap theme color variants to the default spinner. Default is to use the current font color
		*/
		spinnerVariant: {
			type: String
		},
		/**
		* Background theme color variant to use for the overlay backdrop
		*/
		variant: {
			type: String,
			default: 'light'
		},
		/**
		* Element tag to use for the overall wrapper element. Has no effect if prop `no-wrap` is set
		*/
		wrapTag: {
			type: String,
			default: 'div'
		},
		/**
		* Z-index value to apply to the overlay. You may need to increase this value to suit your content or placement
		*/
		zIndex: {
			type: [Number, String] as PropType<Number | String>,
			default: 10
		}
	},
    components: {
        FsSpinner,
    },
    setup(props) {
        const positionCover = { top: 0, left: 0, bottom: 0, right: 0 };

        const computedOverlayClass = computed<any>(() => {
            return ({
                [`b-overlay`]: true,
                [`position-absolute`]: !props.noWrap || (props.noWrap && !props.fixed),
                [`position-fixed`]: props.noWrap && props.fixed
            })
        });

        const computedOverlayStyles = computed<any>(() => {
            return ({
                ...positionCover,
                zIndex: props.zIndex || 10
            })
        });

        const computedRounded = computed<string>(() => {
            return ((props.rounded === true) || (props.rounded === ''))
                ? 'rounded'
                : !props.rounded
                    ? ''
                    : `rounded-${props.rounded}`
        });

        const computedBackgroundClass = computed<any>(() => {
            return ({
                [`position-absolute`]: true,
                [computedRounded.value]: props.rounded,
                [`bg-${props.variant}`]: props.variant && !props.bgColor
            })
        });

        const computedBackgroundStyles = computed<any>(() => {
            return ({
                ...positionCover,
                opacity: props.opacity,
                backgroundColor: props.bgColor || null,
                backdropFilter: props.blur ? `blur(${props.blur})` : null
            })
        });

        const computedContentStyles = computed<any>(() => {
            return props.noCenter ? { ...positionCover }
                : { top: '50%', left: '50%', transform: 'translateX(-50%) translateY(-50%)' };
        });

        const computedSlotScope = computed<any>(() => {
            return ({
                spinnerType: props.spinnerType || null,
                spinnerVariant: props.spinnerVariant || null,
                spinnerSmall: props.spinnerSmall,
            })
        });

        return {
            computedOverlayClass,
            computedOverlayStyles,
            computedRounded,
            computedBackgroundClass,
            computedBackgroundStyles,
            computedContentStyles,
            computedSlotScope
        }
    },
});
