import { computed, defineComponent, inject, Ref } from 'vue';

export default defineComponent({
    name: "FsProgressBar",
    props: {

    /**
     * Enable the animated background. Also automatically sets 'striped'
     */
    animated: {
        type: Boolean,
        default: null
    },
    /**
     * Text string to explicitly set the label as
     */
    label: {
        type: String,
    },
    /**
     *  HTML string to explicitly set the label as
        Use with caution !
        Caution: Props that support HTML strings (*-html) can be vulnerable to
        Cross Site Scripting (XSS) attacks when passed raw user supplied values.
        You must properly sanitize the user input first!
     */
        labelHtml: {
        type: String,
    },

    /**
     * Set the maximum value
     */
    max: {
        type: [Number, String],
        default: null
    },

    /**
     * The number of digits after the decimal to round the value to
     */
    precision: {
        type: [Number, String],
        default: null
    },

    /**
     * Displays the current progress value as a percentage
     */
     showProgress: {
        type: Boolean,
        default: null
    },

    /**
     * Displays the current progress value
     */
     showValue: {
        type: Boolean,
        default: null
    },

    /**
     * Enable the striped background
     */
     striped: {
        type: Boolean,
        default: null
    },

    /**
     * The current value of the progress bar
     */
    value: {
        type: [Number, String],
        default: 0
    },

    /**
     * Applies one of the Bootstrap theme color variants to the component 
     */
     variant: {
        type: String
    },
  },
    setup(props){

      const parent = inject('currentFsProgress') as Ref;
          
      const computedClass = computed<any>(() => {
        return {
            [`progress-bar`]: true,
            [`bg-${computedVariant.value}`]: computedVariant.value,
            [`progress-bar-striped`]: computedStriped.value || computedAnimated.value,
            [`progress-bar-animated`]: computedAnimated.value
        };
      });
      
      const computedStyles = computed<any>(() => {
        return {
            width: 100 * (computedValue.value / computedMax.value) + '%'
        }
      });

      function toFloat(value: any): number {
        const float = parseFloat(value);
        return isNaN(float) ? 0 : float;
      };

      function toInteger(value: any): number {
        const integer = parseInt(value, 10);
        return isNaN(integer) ? 0 : integer;
      };
        
      const computedVariant = computed<string>(() => {
        return props.variant || parent.value?.variant;
      });

      const computedStriped = computed<boolean>(() => {
        return props.striped || parent.value?.striped;
      });

      const computedAnimated = computed<boolean>(() => {
        return props.animated || parent.value?.animated;
      });
        
      const computedValue = computed<number>(() => {
        return toFloat(props.value);
      });

      const computedMax = computed<number>(() => {
        const max = toFloat(props.max) || toFloat(parent.value?.max);
        return max > 0 ? max : 100;
      });

      const computedPrecision = computed<number>(() => {
        const precision = toInteger(props.precision) || toInteger(parent.value?.precision);
        return precision > 0 ? precision : 0;
      });

      const computedProgress = computed<string>(() => {
        const power = Math.pow(10, computedPrecision.value);
        return ((100 * power * computedValue.value) / (computedMax.value * power)).toFixed(computedPrecision.value);
      });

      const computedValueShow = computed<string>(() => {
        return computedValue.value.toFixed(computedPrecision.value);
      });

      const computedLabel = computed<any>(() => {
        if (props.label)
        {
            return props.label;
        }
        if ((props.showProgress) || (parent.value?.showProgress))
        {
            return computedProgress.value;
        }
        if ((props.showValue) || (parent.value?.showValue))
        {
            return computedValue.value.toFixed(computedPrecision.value);
        }

        return "";
      });

      return {
        computedMax,
        computedValueShow,
        computedLabel,
        computedClass,
        computedStyles
      }
    },
})
