import { computed, defineComponent, PropType } from "vue"

export default defineComponent({
    name: "FsListGroup",
    props: {
        /**
        * When set, renders a flush list group with no left and right borders
        */
        flush: {
            type: Boolean,
            default: false
        },
        /**
        * When set, renders the list-group horizontally rather than the default of vertical
        */
        horizontal: {
            type: [Boolean, String] as PropType<Boolean | String>,
            default: false
        },
        /**
        * Specify the HTML tag to render instead of the default tag
        */
        tag: {
            type: String,
            default: 'div'
        }
    },
    emits: [],
    setup(props){
        const calculatedIs = computed<string>(() => {
            return props.tag as string;
        });

        const computedClass = computed<any>(() => {
            return ({
                [`list-group-horizontal-${props.horizontal}`]: typeof props.horizontal === 'string',
                ['list-group-horizontal']: props.horizontal === true,
                ['list-group-flush']: props.flush
            })
        })
        return {
            calculatedIs,
            computedClass
        }
    },
});
