import { computed, defineComponent, PropType } from 'vue'
	
export default defineComponent({
	name: "FsRow",
  props: {
    /**
    * Align columns items together on the cross axis: 'start', 'center', 'end', 'around', 'between' or 'stretch'. Has no effect on single rows of items
    */
    alignContent: {
      type: String,
      default: null
    },
    /**
    * Horizontal alignment/spacing of all columns: 'start', 'center', 'end', 'around', or 'between'
    */
    alignH: {
      type: String,
      default: null
    },
    /**
    * Vertical alignment of all columns in a row: 'start', 'center', 'end', 'baseline', or 'stretch'
    */
    alignV: {
      type: String,
      default: null
    },
    /**
    * The number row columns to create at the 'xs' breakpoint. Requires Bootstrap v4.4 CSS
    */
    cols: {
      type: [Number, String] as PropType<Number | String>,
    },
    /**
    * The number row columns to create at the 'lg' breakpoint. Requires Bootstrap v4.4 CSS
    */
    colsLg: {
      type: [Number, String] as PropType<Number | String>,
    },
    /**
    * The number row columns to create at the 'md' breakpoint. Requires Bootstrap v4.4 CSS
    */
    colsMd: {
      type: [Number, String] as PropType<Number | String>,
    },
    /**
    * The number row columns to create at the 'sm' breakpoint. Requires Bootstrap v4.4 CSS
    */
    colsSm: {
      type: [Number, String] as PropType<Number | String>,
    },
    /**
    * The number row columns to create at the 'xl' breakpoint. Requires Bootstrap v4.4 CSS
    */
    colsXl: {
      type: [Number, String] as PropType<Number | String>,
    },
    /**
    * When set, removes the margin from the row and removes the padding from the child columns
    */
    noGutters: {
      type: Boolean,
      default: false
    },
    /**
    * Specify the HTML tag to render instead of the default tag
    */
    tag: {
      type: String,
      default: 'div'
    },
  },
  emits: [],
	setup(props) {       
    const computedClasses = computed<any>(() => {
      return ({
        [`row-cols-${props.cols}`]: !!props.cols,
        [`row-cols-sm-${props.colsSm}`]: !!props.colsSm,
        [`row-cols-md-${props.colsMd}`]: !!props.colsMd,
        [`row-cols-lg-${props.colsLg}`]: !!props.colsLg,
        [`row-cols-xl-${props.colsXl}`]: !!props.colsXl,
        [`align-content-${props.alignContent}`]: !!props.alignContent,
        [`justify-content-${props.alignH}`]: !!props.alignH,
        [`align-items-${props.alignV}`]: !!props.alignV,
        'g-0': !!props.noGutters,
      });
    })
		
		return {
      computedClasses
		}
	},
})