import { RX_NUMBER } from '../constants/regex'

export const toType = (value: any) => typeof value;

export const isArray = (value: any) => Array.isArray(value);

export const isString = (value: any) => toType(value) === 'string';

// Strict object type check
// Only returns true for plain JavaScript objects
export const isPlainObject = (obj: any) => Object.prototype.toString.call(obj) === '[object Object]';

// Quick object check
// This is primarily used to tell Objects from primitive values
// when we know the value is a JSON-compliant type
// Note object could be a complex type like array, Date, etc.
export const isObject: (obj: any) => boolean = obj => obj !== null && typeof obj === 'object';

export const isDate = value => value instanceof Date;

export const isUndefined = (value: any) => value === undefined;

export const isNull = (value: any) => value === null;

export const isUndefinedOrNull = (value: any) => isUndefined(value) || isNull(value);

export const isFunction = value => toType(value) === 'function';

export const isBoolean = value => toType(value) === 'boolean';

export const isNumber = value => toType(value) === 'number';

export const isNumeric = value => RX_NUMBER.test(String(value));

