import { computed, defineComponent, PropType } from 'vue'

export default defineComponent({
    name: "FsCardGroup",
    props: {
      /**
      * When set, renders the card group in a masonry-like columnar style
      */
      columns: {
        type: Boolean,
        default: false
      },
      /**
      * When set renders the card group with gutters between cards
      */
      deck: {
        type: Boolean,
        default: false
      },
      /**
      * Specify the HTML tag to render instead of the default tag
      */
      tag: {
        type: String,
        default: 'div'
      }
    },
    setup(props){
      const finalClass = computed<any>(() => {
        return props.deck ? 'card-deck' : props.columns ? 'card-columns' : 'card-group'
      })

      return {
        finalClass
      };
    }
})