import { computed, defineComponent, PropType } from 'vue'

	export default defineComponent({
		name: "FsFormGroup",
        props: {
            /**
            * Number of columns for the content width 'xs' screens and up
            */
            contentCols: {
                type: [Boolean, Number, String] as PropType<Boolean | Number | String>
            },
            /**
            * Number of columns for the content width 'lg' screens and up
            */
            contentColsLg: {
                type: [Boolean, Number, String] as PropType<Boolean | Number | String>
            },
            /**
            * Number of columns for the content width 'md' screens and up
            */
            contentColsMd: {
                type: [Boolean, Number, String] as PropType<Boolean | Number | String>
            },
            /**
            * Number of columns for the content width 'sm' screens and up
            */
            contentColsSm: {
                type: [Boolean, Number, String] as PropType<Boolean | Number | String>
            },
            /**
            * Number of columns for the content width 'xl' screens and up
            */
            contentColsXl: {
                type: [Boolean, Number, String] as PropType<Boolean | Number | String>
            },
            /**
            * Text to place in the help text area of the form group
            */
            description: {
                type: String
            },
            /**
            * Disabled the fieldset element, which in turn disables the form controls (on browsers that support disabled fieldsets). Has no effect if `label-for` is set
            */
            disabled: {
                type: Boolean,
                default: false
            },
            /**
            * Value to use for the `aria-live` attribute on the feedback text
            */
            feedbackAriaLive: {
                type: [String] as PropType<"assertive" | "off" | "polite">,
                default: 'assertive'
            },
            /**
            * Used to set the `id` attribute on the rendered content, and used as the base to generate any additional element IDs as needed
            */
            id: {
                type: String
            },
            /**
            * Text to show when the form group has an invalid state
            */
            invalidFeedback: {
                type: String
            },
            /**
            * Text to place in the label/legend of the form group
            */
            label: {
                type: String
            },
            /**
            * Text alignment 'left', 'center', 'right' for the label 'xs' screens and up
            */
            labelAlign: {
                type: String
            },
            /**
            * Text alignment 'left', 'center', 'right' for the label 'lg' screens and up
            */
            labelAlignLg: {
                type: String
            },
            /**
            * Text alignment 'left', 'center', 'right' for the label 'md' screens and up
            */
            labelAlignMd: {
                type: String
            },
            /**
            * Text alignment 'left', 'center', 'right' for the label 'sm' screens and up
            */
            labelAlignSm: {
                type: String
            },
            /**
            * Text alignment 'left', 'center', 'right' for the label 'xl' screens and up
            */
            labelAlignXl: {
                type: String
            },
            /**
            * CSS class (or classes) to add to the label/legend element
            */
            labelClass: {
                type: [Array, Object, String] as PropType<[] | Object | String>
            },
            /**
            * Number of columns for the label width 'xs' screens and up
            */
            labelCols: {
                type: [Boolean, Number, String] as PropType<Boolean | Number | String>
            },
            /**
            * Number of columns for the label width 'lg' screens and up
            */
            labelColsLg: {
                type: [Boolean, Number, String] as PropType<Boolean | Number | String>
            },
            /**
            * Number of columns for the label width 'md' screens and up
            */
            labelColsMd: {
                type: [Boolean, Number, String] as PropType<Boolean | Number | String>
            },
            /**
            * Number of columns for the label width 'sm' screens and up
            */
            labelColsSm: {
                type: [Boolean, Number, String] as PropType<Boolean | Number | String>
            },
            /**
            * Number of columns for the label width 'xl' screens and up
            */
            labelColsXl: {
                type: [Boolean, Number, String] as PropType<Boolean | Number | String>
            },
            /**
            * Set to the ID of the singular form control in the form group. Do not set a value if there is more than one form control in the group
            */
            labelFor: {
                type: String
            },
            /**
            * Sets the text size of the label: 'sm', 'md' (default) or 'lg'. Use this prop to have the label size match the form control size
            */
            labelSize: {
                type: String
            },
            /**
            * Visually hides the label content, but makes it available to screen reader users
            */
            labelSrOnly: {
                type: Boolean,
                default: false
            },
            /**
            * Controls the validation state of the feedback. `true` force shows valid-feedback, `false` force shows invalid feedback, `null` does not force show the feedback
            */
            state: {
                type: Boolean,
                default: null
            },
            /**
            * Renders the feedback text in a rudimentary tooltip style
            */
            tooltip: {
                type: Boolean,
                default: false
            },
            /**
            * Text to show when the form group has a valid state
            */
            validFeedback: {
                type: String
            },
            /**
            * When set, adds the Bootstrap validation trigger class 'was-validated' on the component
            */
            validated: {
                type: Boolean,
                default: false
            }
        },
        emits: [],
		setup(props) {
            const hasLabelColumn = computed<boolean>(() => {
                return !!( props.labelCols ||  props.labelColsSm ||  props.labelColsMd ||  props.labelColsLg ||  props.labelColsXl);
            });

            const hasContentColumn = computed<boolean>(() => {
                return !!( props.contentCols ||  props.contentColsSm ||  props.contentColsMd ||  props.contentColsLg ||  props.contentColsXl);
            });

            const wrapperClasses = computed<any>(() => {
                return ({
                    'row': hasLabelColumn.value || hasContentColumn.value,
                    'is-invalid':  props.state === false,
                    'is-valid':  props.state,
                })
            });

            const labelClasses = computed<any>(() => {
                return ({
                    [`${ props.labelClass}`]: !! props.labelClass,
                    [`text-${ props.labelAlign}`]: !! props.labelAlign,
                    [`text-sm${ props.labelAlign}`]: !! props.labelAlignSm,
                    [`text-md${ props.labelAlign}`]: !! props.labelAlignMd,
                    [`text-lg${ props.labelAlign}`]: !! props.labelAlignLg,
                    [`text-Xl${ props.labelAlign}`]: !! props.labelAlignXl,
                    'col-form-label': hasLabelColumn.value || hasContentColumn.value,
                    'col': !hasLabelColumn.value && hasContentColumn.value,
                    [`col-${ props.labelCols}`]: !! props.labelCols && typeof ( props.labelCols) !== 'boolean',
                    [`col-sm-${ props.labelColsSm}`]: !! props.labelColsSm && typeof ( props.labelColsSm) !== 'boolean',
                    [`col-md-${ props.labelColsMd}`]: !! props.labelColsMd && typeof ( props.labelColsMd) !== 'boolean',
                    [`col-lg-${ props.labelColsLg}`]: !! props.labelColsLg && typeof ( props.labelColsLg) !== 'boolean',
                    [`col-xl-${ props.labelColsXl}`]: !! props.labelColsXl && typeof ( props.labelColsXl) !== 'boolean',
                    'col-sm': !! props.labelColsSm && typeof ( props.labelColsSm) === 'boolean',
                    'col-md': !! props.labelColsMd && typeof ( props.labelColsMd) === 'boolean',
                    'col-lg': !! props.labelColsLg && typeof ( props.labelColsLg) === 'boolean',
                    'col-xl': !! props.labelColsXl && typeof ( props.labelColsXl) === 'boolean',
                })
            });

            const contentClasses = computed<any>(() => {
                return ({
                    'col-form-content': hasLabelColumn.value || hasContentColumn.value,
                    'col': hasLabelColumn.value && !hasContentColumn.value,
                    [`col-${ props.contentCols}`]: !! props.contentCols && typeof ( props.contentCols) !== 'boolean',
                    [`col-sm-${ props.contentColsSm}`]: !! props.contentColsSm && typeof ( props.contentColsSm) !== 'boolean',
                    [`col-md-${ props.contentColsMd}`]: !! props.contentColsMd && typeof ( props.contentColsMd) !== 'boolean',
                    [`col-lg-${ props.contentColsLg}`]: !! props.contentColsLg && typeof ( props.contentColsLg) !== 'boolean',
                    [`col-xl-${ props.contentColsXl}`]: !! props.contentColsXl && typeof ( props.contentColsXl) !== 'boolean',
                    'col-sm': !! props.contentColsSm && typeof ( props.contentColsSm) === 'boolean',
                    'col-md': !! props.contentColsMd && typeof ( props.contentColsMd) === 'boolean',
                    'col-lg': !! props.contentColsLg && typeof ( props.contentColsLg) === 'boolean',
                    'col-xl': !! props.contentColsXl && typeof ( props.contentColsXl) === 'boolean',
                })
            });

            const feedbackClasses = computed<any>(() => {
                return ({
                    'invalid-feedback': ! props.state,
                    'valid-feedback':  props.state,
                })
            });

			return {
                wrapperClasses,
                labelClasses,
                contentClasses,
                feedbackClasses
			}
		},
	})