import { computed, defineComponent, PropType } from 'vue'

export default defineComponent({
    name: "FsDropdownForm",
    props: {
        /**
        * When set to `true`, disables the component's functionality and places it in a disabled state
        */
        disabled: {
            type: Boolean,
            default: false
        },
        /**
        * CSS class (or classes) to add to the form
        */
        formClass: {
            type: [Array, Object, String] as PropType<[] | Object | String>
        },
        /**
        * Used to set the `id` attribute on the rendered content, and used as the base to generate any additional element IDs as needed
        */
        id: {
            type: String
        },
        /**
        * When set, the form will be in inline mode which display labels, form controls, and buttons on a single horizontal row
        */
        inline: {
            type: Boolean,
            default: false
        },
        /**
        * When set, disables browser native HTML5 validation on controls in the form
        */
        novalidate: {
            type: Boolean,
            default: false
        },
        /**
        * When set, adds the Bootstrap class 'was-validated' on the form, triggering the native browser validation states
        */
        validated: {
            type: Boolean,
            default: false
        }
    },
    emits: [],
    setup(props){
        const finalClass = computed<any>(() => {
            return ({
                'form-inline': props.inline,
            })
        });

        return {
          finalClass
        };

    }
})