import { computed, defineComponent, PropType } from 'vue'

export default defineComponent({
    name: "FsDropdownItemButton",
    props: {
        /**
        * When set to `true`, places the component in the active state with active styling
        */
        active: {
            type: Boolean,
            default: false
        },
        /**
        * <router-link> prop: Configure the active CSS class applied when the link is active. Typically you will want to set this to class name 'active'
        */
        activeClass: {
            type: String,
            default: 'active'
        },
        /**
        * Class or classes to apply to the inner button element
        */
        buttonClass: {
            type: [Array, Object, String] as PropType<[] | Object | String>
        },
        /**
        * When set to `true`, disables the component's functionality and places it in a disabled state
        */
        disabled: {
            type: Boolean,
            default: false
        },
        /**
        * Applies one of the Bootstrap theme color variants to the component
        */
        variant: {
            type: String
        }
    },
    emits: ['click'],
    setup(props, { emit }){
        const finalClass = computed<any>(() => {
            return ({
                [`${props.activeClass}`]: props.active,
                disabled: props.disabled,
                [`text-${props.variant}`]: props.variant,
            })
        });
    
        const finalAttrs = computed<any>(() => {
            return ({
                role: 'menuitem',
                type: 'button',
                disabled: props.disabled,
            })
        });

        function clicked(e: PointerEvent): void {
            if (props.disabled) {
                e.preventDefault()
                e.stopPropagation()
                return
            }
            emit('click', e)
        }
          
      return {
        finalClass,
        finalAttrs,
        clicked
      };
    }
})