import { computed, defineComponent, PropType, watch, nextTick, ref, onMounted, onBeforeUnmount } from 'vue'
import generateId from '../../utils/generateId';
import { useDirectiveBus } from './../../composables';
import { useRoute } from 'vue-router'
	
	export default defineComponent({
        name: "FsSidebar",
        props: {
            /**
            * Sets the value of `aria-label` attribute on the rendered element
            */
            ariaLabel: {
                type: String
            },
            /**
            * The ID of the element that provides a label for this component. Used as the value for the `aria-labelledby` attribute
            */
            ariaLabelledby: {
                type: String
            },
            /**
            * When `true`, shows a backdrop when the sidebar is open
            */
            backdrop: {
                type: Boolean,
                default: false
            },
            /**
            * Theme variant color for the backdrop of the sidebar. Defaults to 'dark'
            */
            backdropVariant: {
                type: String,
            },
            /**
            * Theme variant color for the background of the sidebar
            */
            bgVariant: {
                type: String,
                default: 'light'
            },
            /**
            * Class, or classes, to apply to the body (default slot) of the sidebar
            */
            bodyClass: {
                type: [Array, Object, String] as PropType<[] | Object | String>
            },
            /**
            * `aria-label` to apply to the built-in close button. Defaults to 'Close'
            */
            closeLabel: {
                type: String
            },
            /**
            * Class, or classes, to apply to the optional `footer` slot
            */
            footerClass: {
                type: [Array, Object, String] as PropType<[] | Object | String>
            },
            /**
            * Specify the HTML tag to render instead of the default tag for the footer
            */
            footerTag: {
                type: String,
                default: 'footer'
            },
            /**
            * Class, or classes, to apply to the built in header. Has no effect if prop `no-header` is set
            */
            headerClass: {
                type: [Array, Object, String] as PropType<[] | Object | String>
            },
            /**
            * Specify the HTML tag to render instead of the default tag for the footer
            */
            headerTag: {
                type: String,
                default: 'header'
            },
            /**
            * Used to set the `id` attribute on the rendered content, and used as the base to generate any additional element IDs as needed
            */
            id: {
                type: String
            },
            /**
            * When set to `true`, the content of the sidebar will only be rendered while the sidebar is open
            */
            lazy: {
                type: Boolean,
                default: false
            },
            modelValue: {
                type: Boolean,
                default: false
            },
            /**
            * When set to `true`, disables closing the sidebar when the user clicks on the backdrop. Requires that the prop `backdrop` be set
            */
            noCloseOnBackdrop: {
                type: Boolean,
                default: false
            },
            /**
            * When set to `true`, disables closing the sidebar when the user presses ESC
            */
            noCloseOnEsc: {
                type: Boolean,
                default: false
            },
            /**
            * When set to `true`, disables closing of the sidebar on route change
            */
            noCloseOnRouteChange: {
                type: Boolean,
                default: false
            },
            /**
            * Disables the enforce focus routine which maintains focus inside the sidebar
            */
            noEnforceFocus: {
                type: Boolean,
                default: false
            },
            /**
            * When set to `true` disables rendering of the default header (including close button)
            */
            noHeader: {
                type: Boolean,
                default: false
            },
            /**
            * When set to `true` disables rendering of the header close button
            */
            noHeaderClose: {
                type: Boolean,
                default: false
            },
            /**
            * When set, disables the default sliding animation
            */
            noSlide: {
                type: Boolean,
                default: false
            },
            /**
            * When `true`, positions the sidebar on the right of the viewport
            */
            right: {
                type: Boolean,
                default: false
            },
            /**
            * Set to boolean `true` for medium shadow, 'sm' for small shadow, 'lg' for large shadow, or boolean `false` for no shadow. Default is no shadow
            */
            shadow: {
                type: [Boolean, String] as PropType<Boolean | String>,
                default: false
            },
            /**
            * Class, or classes, to apply to the sidebar content wrapper
            */
            sidebarClass: {
                type: [Array, Object, String] as PropType<[] | Object | String>
            },
            /**
            * Specify the HTML tag to render instead of the default tag
            */
            tag: {
                type: String,
                default: 'div'
            },
            /**
            * Theme variant color for the text of the sidebar
            */
            textVariant: {
                type: String,
                default: 'dark'
            },
            /**
            * Text content to place in the default header. The `title` slot takes precedence
            */
            title: {
                type: String
            },
            /**
            * When `true`, opens the sidebar. This is the `v-model`
            */
            visible: {
                type: Boolean,
                default: false
            },
            /**
            * CSS width for the sidebar. Defaults to '320px' as defined by SCSS/CSS
            */
            width: {
                type: String
            },
            /**
            * Specify an arbitrary z-index value to override the value defined by SCSS/CSS
            */
            zIndex: {
                type: [Number, String] as PropType<Number | String>
            }
        },
		emits: ['shown', 'hidden', 'change', 'update:modelValue', 'update:visible'],					
		setup(props, { emit }) {  
            const element = ref(null);
            const localVisible = ref(false);

            const { directiveBus } = useDirectiveBus();
            const route = useRoute();

            const getValue = computed<boolean>(() => {
                return props.visible;
            });
        
            const getWidth = computed<any>(() => {
                return props.width ? { width: props.width } : '';
            });

            const transitionProps = computed<any>(() => {
                return props.noSlide ?
                    {
                        css: true
                    } :
                    {
                        css: true,
                        enterClass: '',
                        enterActiveClass: 'slide',
                        enterToClass: 'show',
                        leaveClass: 'show',
                        leaveActiveClass: 'slide',
                        leaveToClass: ''
                    }
            });

            const backdropProps = computed<any>(() => {
                return {
                        css: true,
                        enterClass: '',
                        enterActiveClass: 'fade',
                        enterToClass: 'show',
                        leaveClass: 'show',
                        leaveActiveClass: 'fade',
                        leaveToClass: ''
                    }
            });
        
            const sidebarId = computed<String>(() => {
                return props.id ? props.id as string : generateId();
            });

            const sidebarTitleId = computed<string>(() => {
                return props.id + '__title_';
            });
        
            const outerStyle = computed<any>(() => {
                return props.zIndex ? { zIndex: props.zIndex } : '';
            });
        
            const finalClass = computed<any>(() => {
                return ({
                    'fs-sidebar-right': props.right,
                    ['bg-' + props.bgVariant]: props.bgVariant,
                    ['text-' + props.textVariant]: props.textVariant,
                    'shadow': props.shadow === true,
                    ['shadow-' + props.shadow]: props.shadow && props.shadow !== true,
                })
            });
        
            const finalBackdropClass = computed<any>(() => {
                return ({
                    'bg-opacity-50': props.backdropVariant,
                    ['bg-' + props.backdropVariant]: props.backdropVariant,
                })
            });

            onMounted(() =>
            {
                window.addEventListener('keydown', onKeydown);
            })

            onBeforeUnmount(() =>
            {
                window.removeEventListener('keydown', onKeydown);
            })

            
            watch(()=> directiveBus.value.get(`${sidebarId.value}`), (val) => {
                if(val){
                    toggle(); 
                 }
            }, { deep: true })

            watch(() => route?.fullPath, 
            (newValue: any, oldValue: any) =>
            {
                if(!props.noCloseOnRouteChange && newValue !== oldValue) {
                    updateVisible(false);
                }
            })

            watch(() => props.modelValue, 
             (visible: boolean) => {
                if (visible) {
                    emit('shown', visible);
                } else {
                    emit('hidden', visible);
                }

                emit('change', visible);
                localVisible.value = visible;

                if (!props.noEnforceFocus && visible) {
                    const sidebar: HTMLElement = (element.value as HTMLElement);
                    if (sidebar) {
                        nextTick(() => {
                            sidebar.focus();
                        })
                    }
                }
            },{ immediate: true });

            watch(() => props.visible, 
            (visible: boolean) => {
                if (visible) {
                    emit('shown', visible);
                } else {
                    emit('hidden', visible);
                }
        
                emit('change', visible);
                localVisible.value = visible;
        
                if (!props.noEnforceFocus && visible) {
                    const sidebar: HTMLElement = (element.value as HTMLElement);
                    if (sidebar) {
                        nextTick(() => {
                            sidebar.focus();
                        })
                    }
                }
            },{ immediate: true })
        
               
            function close(): void {
                updateVisible(false);
            }
        
            function backdropClose(): void {
                if (!props.noCloseOnBackdrop) {
                    updateVisible(false);
                }
            }
        
            function hide() : void {
                updateVisible(false);
            }
        
            function onKeydown(event: KeyboardEvent) {
                if (event.key === 'Escape' && !props.noCloseOnEsc) {
                    updateVisible(false);
                }
            }
        
            function updateVisible(value: boolean) {
                localVisible.value = value;
                emit('update:modelValue', value);
                emit('change', value);
                if(!value && props.visible){
                    emit('update:visible', value);
                }
            }

            function toggle(){
                updateVisible(!localVisible.value);
            }

			return {
                element,
                getWidth,
                getValue,
                transitionProps,
                backdropProps,
                sidebarId,
                sidebarTitleId,
                outerStyle,
                finalClass,
                finalBackdropClass,
                localVisible,
                close,
                backdropClose,
                hide, 
                toggle,
            }
		},
	})