import { computed, defineComponent, PropType, ref } from 'vue'

export default defineComponent({
    name: "FsCard",
    props: {
      /**
      * Text alignment for the card's content: 'left', 'center' or 'right'
      */
      align: {
        type: String
      },
      /**
      * Applies one of the Bootstrap theme color variants to the background
      */
      bgVariant: {
        type: String
      },
      /**
      * Applies one of the Bootstrap theme color variants to the body background
      */
      bodyBgVariant: {
        type: String
      },
      /**
      * Applies one of the Bootstrap theme color variants to the body border
      */
      bodyBorderVariant: {
        type: String
      },
      /**
      * CSS class (or classes) to apply to the body
      */
      bodyClass: {
        type: [Array, Object, String] as PropType<[] | Object | String>
      },
      /**
      * Specify the HTML tag to render instead of the default tag for the body
      */
      bodyTag: {
        type: String,
        default: 'div'
      },
      /**
      * Applies one of the Bootstrap theme color variants to the body text
      */
      bodyTextVariant: {
        type: String
      },
      /**
      * Applies one of the Bootstrap theme color variants to the border
      */
      borderVariant: {
        type: String
      },
      /**
      * Text content to place in the footer
      */
      footer: {
        type: String
      },
      /**
      * Applies one of the Bootstrap theme color variants to the footer background
      */
      footerBgVariant: {
        type: String
      },
      /**
      * Applies one of the Bootstrap theme color variants to the footer border
      */
      footerBorderVariant: {
        type: String
      },
      /**
      * CSS class (or classes) to apply to the footer
      */
      footerClass: {
        type: [Array, Object, String] as PropType<[] | Object | String>
      },
      /**
      * HTML string content to place in the footer
      */
      footerHtml: {
        type: String
      },
      /**
      * Specify the HTML tag to render instead of the default tag for the footer
      */
      footerTag: {
        type: String,
        default: 'div'
      },
      /**
      * Applies one of the Bootstrap theme color variants to the footer text
      */
      footerTextVariant: {
        type: String
      },
      /**
      * Text content to place in the header
      */
      header: {
        type: String
      },
      /**
      * Applies one of the Bootstrap theme color variants to the header background
      */
      headerBgVariant: {
        type: String
      },
      /**
      * Applies one of the Bootstrap theme color variants to the header border
      */
      headerBorderVariant: {
        type: String
      },
      /**
      * CSS class (or classes) to apply to the header
      */
      headerClass: {
        type: [Array, Object, String] as PropType<[] | Object | String>
      },
      /**
      * HTML string content to place in the header
      */
      headerHtml: {
        type: String
      },
      /**
      * Specify the HTML tag to render instead of the default tag for the header
      */
      headerTag: {
        type: String,
        default: 'div'
      },
      /**
      * Applies one of the Bootstrap theme color variants to the header text
      */
      headerTextVariant: {
        type: String
      },
      /**
      * Value to set the image attribute 'alt'
      */
      imgAlt: {
        type: String
      },
      /**
      * Set if the image should appear at the bottom of the card
      */
      imgBottom: {
        type: Boolean,
        default: false
      },
      /**
      * Set if the image should appear at the end (right) of the card
      */
      imgEnd: {
        type: Boolean,
        default: false
      },
      /**
      * The value to set on the image's 'height' attribute
      */
      imgHeight: {
        type: Number,
      },
      /**
      * Set if the image should appear at the start (left) of the card. Synonym for the 'left' prop
      */
      imgLeft: {
        type: Boolean,
        default: false
      },
      /**
      * Set if the image should appear at the end (right) of the card. Synonym for the 'right' prop
      */
      imgRight: {
        type: Boolean,
        default: false
      },
      /**
      * URL for the optional image
      */
      imgSrc: {
        type: String
      },
      /**
      * Set if the image should appear at the start (left) of the card
      */
      imgStart: {
        type: Boolean,
        default: false
      },
      /**
      * Set if the image should appear at the top of the card
      */
      imgTop: {
        type: Boolean,
        default: false
      },
      /**
      * The value to set on the image's 'width' attribute
      */
      imgWidth: {
        type: Number,
      },
      /**
      * Disable rendering of the default inner card-body element
      */
      noBody: {
        type: Boolean,
        default: false
      },
      /**
      * When set, will overlay the card body on top of the image (if the card has an image)
      */
      overlay: {
        type: Boolean,
        default: false
      },
      /**
      * Text content to place in the sub title
      */
      subTitle: {
        type: String
      },
      /**
      * Specify the HTML tag to render instead of the default tag for the sub title
      */
      subTitleTag: {
        type: String,
        default: 'h6'
      },
      /**
      * Applies one of the Bootstrap theme color variants to the sub title text
      */
      subTitleTextVariant: {
        type: String,
        default: 'muted'
      },
      /**
      * Specify the HTML tag to render instead of the default tag
      */
      tag: {
        type: String,
        default: 'div'
      },
      /**
      * Applies one of the Bootstrap theme color variants to the text
      */
      textVariant: {
        type: String
      },
      /**
      * Text content to place in the title
      */
      title: {
        type: String
      },
      /**
      * Specify the HTML tag to render instead of the default tag for the title
      */
      titleTag: {
        type: String,
        default: 'h4'
      }
    },
    emits: ['click'],
    setup(props, { slots, emit }){
      const hasFooter = ref(false);
      const hasHeader = ref(false);

      const finalClass = computed<any>(() => {
        return ({
          ['text-' + props.align]: props.align,
          ['bg-' + props.bgVariant]: props.bgVariant,
          ['text-' + props.textVariant]: props.textVariant,
          ['border-' + props.borderVariant]: props.borderVariant,
          'flex-row-reverse': props.imgEnd || props.imgRight,
          'flex-row': props.imgLeft || props.imgStart,
        })
      });
    
      const finalBodyClass = computed<any>(() => {
        return ({
          ['bg-' + props.bodyBgVariant]: props.bodyBgVariant,
          ['border-' + props.bodyBorderVariant]: props.bodyBorderVariant,
          ['text-' + props.bodyTextVariant]: props.bodyTextVariant,
          'card-img-overlay': props.overlay,
        })
      });
    
      const finalFooterClass = computed<any>(() => {
        return ({
          ['bg-' + props.footerBgVariant]: props.footerBgVariant,
          ['border-' + props.footerBorderVariant]: props.footerBorderVariant,
          ['text-' + props.footerTextVariant]: props.footerTextVariant,
        })
      });
    
      const finalHeaderClass = computed<any>(() => {
        return ({
          ['bg-' + props.headerBgVariant]: props.headerBgVariant,
          ['border-' + props.headerBorderVariant]: props.headerBorderVariant,
          ['text-' + props.headerTextVariant]: props.headerTextVariant,
        })
      });
    
      const finalImgClass = computed<any>(() => {
        return ({
          'card-img': !props.imgEnd && !props.imgRight && !props.imgStart && !props.imgLeft &&!props.imgTop,
          'card-img-bottom': props.imgBottom,
          'card-img-right': props.imgEnd || props.imgRight,
          'card-img-left': props.imgLeft || props.imgStart,
          'card-img-top': props.imgTop,
        })
      });
    
      const finalSubTitleClass = computed<any>(() => {
        return ({
          ['text-' + props.subTitleTextVariant]: props.subTitleTextVariant,
        })
      });

      if (slots.footer && slots.footer().length) {
        hasFooter.value = true;
      }

      if (slots.header && slots.header().length) {
        hasHeader.value = true;
      }

      function clicked(...args: any[]) {
        emit('click', args);
      }

      return {
        finalClass,
        finalBodyClass,
        finalFooterClass,
        finalHeaderClass,
        finalImgClass,
        finalSubTitleClass,
        hasHeader,
        hasFooter,
        clicked
      };
      
    }
  })