import { computed, defineComponent, onBeforeUnmount, PropType, ref, Ref, watch } from 'vue'

export default defineComponent({
  name: "FsPagination",
  props: {
    /**
    * Alignment of the page buttons: 'start' (or 'left'), 'center', 'end' (or 'right'), or 'fill'
    */
    align: {
      type: String,
      default: 'left'
    },
    /**
    * If this component controls another component or element, set this to the ID of the controlled component or element
    */
    ariaControls: {
      type: String
    },
    /**
    * Value to place in the 'aria-label' attribute of the pagination control
    */
    ariaLabel: {
      type: String,
      default: 'Pagination'
    },
    /**
    * When set to `true`, disables the component's functionality and places it in a disabled state
    */
    disabled: {
      type: Boolean,
      default: false
    },
    /**
    * Class(es) to apply to the 'ellipsis' placeholders
    */
    ellipsisClass: {
      type: [Array, Object, String] as PropType<[] | Object | String>
    },
    /**
    * Content to place in the ellipsis placeholder
    */
    ellipsisText: {
      type: String,
      default: '…'
    },
    /**
    * Class(es) to apply to the 'Go to first page' button
    */
    firstClass: {
      type: [Array, Object, String] as PropType<[] | Object | String>
    },
    /**
    * Display first page number instead of Goto First button
    */
    firstNumber: {
      type: Boolean,
      default: false
    },
    /**
    * Content to place in the goto first page button
    */
    firstText: {
      type: String,
      default: '«'
    },
    /**
    * Do not show ellipsis buttons
    */
    hideEllipsis: {
      type: Boolean,
      default: false
    },
    /**
    * Hides the goto first and goto last page buttons
    */
    hideGotoEndButtons: {
      type: Boolean,
      default: false
    },
    /**
    * Value to place in the 'aria-label' attribute of the goto first page button
    */
    labelFirstPage: {
      type: String,
      default: 'Go to first page'
    },
    /**
    * Value to place in the 'aria-label' attribute of the goto last page button
    */
    labelLastPage: {
      type: String,
      default: 'Go to last page'
    },
    /**
    * Value to place in the 'aria-label' attribute of the goto next page button
    */
    labelNextPage: {
      type: String,
      default: 'Go to next page'
    },
    /**
    * Value to place in the 'aria-label' attribute of the goto page button. Page number will be prepended automatically
    */
    labelPage: {
      type: [Function, String] as PropType<Function | String>,
      default: 'Go to page'
    },
    /**
    * Value to place in the 'aria-label' attribute of the goto previous page button
    */
    labelPrevPage: {
      type: String,
      default: 'Go to previous page'
    },
    /**
    * Class(es) to apply to the 'Go to last page' button
    */
    lastClass: {
      type: [Array, Object, String] as PropType<[] | Object | String>
    },
    /**
    * Display last page number instead of Goto Last button
    */
    lastNumber: {
      type: Boolean,
      default: false
    },
    /**
    * Content to place in the goto last page button
    */
    lastText: {
      type: String,
      default: '»'
    },
    /**
    * Maximum number of buttons to show (including ellipsis if shown, but excluding the bookend buttons)
    */
    limit: {
      type: [Number, String] as PropType<Number | String>,
      default: 5
    },
    /**
    * Class(es) to apply to the 'Go to next page' button
    */
    nextClass: {
      type: [Array, Object, String] as PropType<[] | Object | String>
    },
    /**
    * Content to place in the goto next page button
    */
    nextText: {
      type: String,
      default: '›'
    },
    /**
    * Class(es) to apply to the 'Go to page #' buttons
    */
    pageClass: {
      type: [Array, Object, String] as PropType<[] | Object | String>
    },
    /**
    * Number of rows per page
    */
    perPage: {
      type: [Number, String] as PropType<Number | String>,
      default: 20
    },
    /**
    * Applies pill styling to the pagination buttons
    */
    pills: {
      type: Boolean,
      default: false
    },
    /**
    * Class(es) to apply to the 'Go to previous page' button
    */
    prevClass: {
      type: [Array, Object, String] as PropType<[] | Object | String>
    },
    /**
    * Content to place in the goto previous page button
    */
    prevText: {
      type: String,
      default: '‹'
    },
    /**
    * Size of the rendered buttons: 'sm', 'md' (default), or 'lg'
    */
    size: {
      type: String
    },
    /**
    * Total number of rows in the dataset
    */
    totalRows: {
      type: [Number, String] as PropType<Number | String>,
      default: 0
    },
    /**
    * Current page number, starting from 1
    */
    modelValue: {
      type: [Boolean, Number, String] as PropType<Boolean | Number | String>,
      default: null
    },
  },
  emits: ['update:modelValue', 'change', 'page-click'],
  setup(props, { emit }) {
    const element: Ref<any> = ref(null); // ref on template
    const currentPage: Ref<number> = ref(null);
    const ELLIPSIS_THRESHOLD: number = 3;

    const finalClass = computed<any>(() => {
      return {
        'justify-content-start': props.align === 'start' || props.align === 'left',
        'justify-content-end': props.align === 'end' || props.align === 'right',
        'justify-content-center': props.align === 'center',
        'text-center': props.align === 'fill',
        'b-pagination-pills': props.pills,
        ['pagination-' + `${props.size}`]: props.size,
      };
    });

    const finalBtnClass = computed<any>(() => {
      return {
        'flex-fill': props.align === 'fill',
      };
    });

    const finalEllipsisClass = computed<any>(() => {
      return {
        'page-item': true,
        'flex-fill d-flex': props.align === 'fill',
        'disabled': props.disabled,
        [`${props.ellipsisClass}`]: props.ellipsisClass,
      };
    });

    const finalGoToFirstClass = computed<any>(() => {
      return {
        'page-item': true,
        'flex-fill d-flex': props.align === 'fill',
        'disabled': props.disabled,
        [`${props.firstClass}`]: props.firstClass,
      };
    });

    const finalListItemClass = computed<any>(() => {
      return {
        'page-item': true,
        'flex-fill d-flex': props.align === 'fill',
        'disabled': props.disabled,
      };
    });

    const finalGoToNextClass = computed<any>(() => {
      return {
        'page-item': true,
        'flex-fill d-flex': props.align === 'fill',
        'disabled': props.disabled,
        [`${props.nextClass}`]: props.nextClass
      };
    });

    const finalGoToPrevClass = computed<any>(() => {
      return {
        'page-item': true,
        'flex-fill d-flex': props.align === 'fill',
        'disabled': props.disabled,
        [`${props.prevClass}`]: props.prevClass
      };
    });

    const finalLastClass = computed<any>(() => {
      return {
        'page-item': true,
        'flex-fill d-flex': props.align === 'fill',
        'disabled': props.disabled,
        [`${props.lastClass}`]: props.lastClass
      };
    });

    const finalListItemBtnClass = computed<any>(() => {
      return {
        'flex-grow-1': props.align === 'fill',

      };
    });

    const numberOfPages = computed<number>(() => {
      const totRows = Number(props.totalRows);
      const pPage = Number(props.perPage);
      const numPages = Math.ceil(totRows / pPage);

      return numPages;
    });

    const pages = computed<any[]>(() => {
      const result = []
      for (let i = 0; i < numberOfLinks.value; i++) {
        result.push({ number: startNumber.value + i })
      }

      return result
    });

    const getPages = computed<any[]>(() => {
      let pagesToDisplay = [];
      const pageNumbers = pages.value.map((p) => p.number)


      for (let i = 1; i <= numberOfLinks.value; i++) {
        const pageItem = 'page-item ';
        const active = pageNumbers[i - 1] === props.modelValue ? 'active ' : '';
        const alignment = props.align === 'fill' ? 'flex-fill d-flex' : '';
        const disabled = props.disabled ? 'disabled' : '';
        const liPageClass = props.pageClass ? [`${props.pageClass}`] : '';
        const finalString = pageItem + active + alignment + disabled + liPageClass;
        const offset = showFirstEllipsis.value && props.firstNumber && pageNumbers[0] !== 1 ? 1 : 0

        pagesToDisplay.push({
          id: i + offset,
          number: pageNumbers[i - 1],
          isSelected: pageNumbers[i - 1] === props.modelValue ? true : false,
          class: finalString,
        });
      }

      return pagesToDisplay;
    });

    const numberOfLinks = computed<number>(() => {
      let propLimit = props.limit;

      if (numberOfPages.value <= props.limit) {
        propLimit = numberOfPages.value
      } else if (props.modelValue < Number(props.limit) - 1 && props.limit > ELLIPSIS_THRESHOLD) {
        if (!props.hideEllipsis || props.lastNumber) {
          propLimit = Number(props.limit) - (props.firstNumber ? 0 : 1)
        }
        propLimit = Math.min(Number(propLimit), Number(props.limit))
      } else if (
        numberOfPages.value - Number(props.modelValue) + 2 < props.limit &&
        props.limit > ELLIPSIS_THRESHOLD
      ) {
        if (!props.hideEllipsis || props.firstNumber) {
          propLimit = Number(props.limit) - (props.lastNumber ? 0 : 1)
        }
      } else {
        if (props.limit > ELLIPSIS_THRESHOLD) {
          propLimit = Number(props.limit) - (props.hideEllipsis ? 0 : 2)
        }
      }

      return Number(propLimit);
    });

    const startNumber = computed<number>(() => {
      let startingNumber = 1;
      const pagesLeft: number = numberOfPages.value - Number(props.modelValue);

      if (pagesLeft + 2 < props.limit && props.limit > ELLIPSIS_THRESHOLD) {
        startingNumber = numberOfPages.value - numberOfLinks.value + 1;
      } else {
        startingNumber = Number(props.modelValue) - Math.floor(numberOfLinks.value / 2);
      }

      if (startingNumber < 1) {
        startingNumber = 1;
      } else if (startingNumber > numberOfPages.value - numberOfLinks.value) {
        startingNumber = numberOfPages.value - numberOfLinks.value + 1;
      }

      if (props.limit <= ELLIPSIS_THRESHOLD) {
        if (props.lastNumber && numberOfPages.value === startingNumber + numberOfLinks.value - 1) {
          startingNumber = Math.max(startingNumber - 1, 1);
        }
      }

      return startingNumber
    });

    const showFirstEllipsis = computed<boolean>(() => {
      const pagesLeft = numberOfPages.value - Number(props.modelValue);
      let showDots = false;
      if (pagesLeft + 2 < props.limit && props.limit > ELLIPSIS_THRESHOLD) {
        if (props.limit > ELLIPSIS_THRESHOLD) {
          showDots = true;
        }
      } else {
        if (props.limit > ELLIPSIS_THRESHOLD) {
          showDots = !!(!props.hideEllipsis || props.firstNumber);
        }
      }
      if (startNumber.value <= 1) {
        showDots = false;
      }
      if (showDots && props.firstNumber && startNumber.value < 4) {
        showDots = false;
      }
      return showDots;
    });

    const showLastEllipsis = computed<boolean>(() => {
      const paginationEnd = numberOfPages.value - numberOfLinks.value;
      let showDots = false
      if (props.modelValue < Number(props.limit) - 1 && props.limit > ELLIPSIS_THRESHOLD) {
        if (!props.hideEllipsis || props.lastNumber) {
          showDots = true
        }
      } else {
        if (props.limit > ELLIPSIS_THRESHOLD) {
          showDots = !!(!props.hideEllipsis || props.lastNumber)
        }
      }
      if (startNumber.value > paginationEnd) {
        showDots = false
      }
      const lastPageNumber = startNumber.value + numberOfLinks.value - 1
      if (showDots && props.lastNumber && lastPageNumber > numberOfPages.value - 3) {
        showDots = false
      }
      return showDots
    });

    watch(() => props.modelValue,
      (newValue: number) => {
        if (newValue !== currentPage.value) {
          currentPage.value = newValue;
          emit('change', currentPage.value);
        }
      });


    onBeforeUnmount(() => {
      window.removeEventListener('keydown', onKeypress);
    });

    function onFirstText(): void {
      emit('update:modelValue', 1);
      currentPage.value = 1;
    };

    function onPrevText(): void {
      if (currentPage.value > 1) {
        currentPage.value = currentPage.value - 1;
        emit('update:modelValue', currentPage.value);
      }
    };

    function onNextText(): void {
      if (currentPage.value < numberOfPages.value) {
        currentPage.value = currentPage.value + 1;
        emit('update:modelValue', currentPage.value);
      }
    };

    function onLastText(): void {
      currentPage.value = numberOfPages.value;
      emit('update:modelValue', currentPage.value);
    };

    function buttonClick(page: any, e: PointerEvent): void {
      emit('update:modelValue', page.number); 
      emit('page-click', e, page.number);
      setTimeout(attemptFocus, 100);
    };

    function attemptFocus(): void {
      const btn: HTMLElement = (element.value.querySelector('.active').firstChild as HTMLElement);
      btn.focus();
    };

    function onKeypress(event: KeyboardEvent): void {
      let focusedElement = document.activeElement as HTMLElement;

      if (focusedElement.className.includes("page-link")) {
        if (event.key !== 'Tab') {
          event.preventDefault();
        }

        switch (event.key) {
          case 'ArrowRight':
          case 'ArrowDown': {
            if (focusedElement.parentElement.parentElement.lastChild as HTMLElement !== focusedElement.parentElement as HTMLElement) {
              var next = focusedElement.parentElement.nextSibling as HTMLElement;
              if (next.childNodes.length !== 0 && next.className.includes("dots")) {
                focusedElement = focusedElement.parentElement.nextSibling.nextSibling.firstChild as HTMLElement;
              }
              else {
                focusedElement = focusedElement.parentElement.nextElementSibling.firstChild as HTMLElement;
              }
              if (focusedElement) {
                focusedElement.focus();
              }
            }
            break;
          }
          case 'ArrowUp':
          case 'ArrowLeft': {
            if (focusedElement.parentElement.parentElement.firstChild as HTMLElement !== focusedElement.parentElement as HTMLElement) {
              var prev = focusedElement.parentElement.previousSibling as HTMLElement;
              if (prev.childNodes.length !== 0 && prev.className.includes("dots")) {
                focusedElement = focusedElement.parentElement.previousSibling.previousSibling.firstChild as HTMLElement;
              }
              else {
                focusedElement = focusedElement.parentElement.previousElementSibling.firstChild as HTMLElement;
              }
              if (focusedElement) {
                focusedElement.focus();
              }
            }
            break;
          }
          case ' ':
          case 'Enter': {
            focusedElement.click();
            break;
          }
        }
      }
    };

    window.addEventListener('keydown', onKeypress);
    currentPage.value = Number(props.modelValue);

    return {
      element,
      finalClass,
      finalBtnClass,
      finalEllipsisClass,
      finalGoToFirstClass,
      finalListItemClass,
      finalGoToNextClass,
      finalGoToPrevClass,
      finalLastClass,
      finalListItemBtnClass,
      getPages,
      showFirstEllipsis,
      showLastEllipsis,
      onFirstText,
      onPrevText,
      onNextText,
      onLastText,
      buttonClick
    }
  },
})