import { defineComponent, PropType } from 'vue'

export default defineComponent({
    name: "FsContainer",
    props: {
      /**
      * When set to true, makes the row 100% wide all the time, or set to one of the Bootstrap breakpoint names for 100% width up to 
      * the breakpoint (requires Bootstrap v4.4+ CSS for breakpoint specific value)
      */
      fluid: {
        type: Boolean,
        default: false,
      },
      /**
      * Specify the HTML tag to render instead of the default tag
      */
      tag: {
        type: String,
        default: 'div',
      },
      propA: Number,
      propB: {
        type: String,
        default: 'default value',
      },
      propC: {
        type: [String, Boolean] as PropType<String | Boolean>,
      },
    },
    emits: []
})