import { defineComponent } from 'vue'

export default defineComponent({
    name: "FsCardTitle",
    props: {
        /**
        * Text content to place in the title
        */
        title: {
            type: String
        },
        /**
        * Specify the HTML tag to render instead of the default tag for the title
        */
        titleTag: {
            type: String,
            default: 'h4'
        }
    }
})