import { computed, defineComponent, onBeforeUnmount, PropType, ref, Ref, watch } from 'vue'

export default defineComponent({
    name: "FsAspect",
    props: {
        /**
        * Aspect as a width to height numeric ratio (such as `1.5`) or `width:height` string (such as '16:9')
        */
        aspect: {
            type: [Number, String] as PropType<Number | String>,
            default: '1:1'
        },
        /**
        * Specify the HTML tag to render instead of the default tag
        */        
        tag: {
            type: String,
            default: 'div'
        }
    },
    setup(props){

        const padding = computed<any>(() => {
            const RX_ASPECT = /^\d+(\.\d*)?[/:]\d+(\.\d*)?$/;
            const RX_ASPECT_SEPARATOR = /[/:]/;

            const aspect =  props.aspect.toString();
            let ratio = 1;
    
            if (RX_ASPECT.test(aspect)) {
                const [width, height] = aspect.split(RX_ASPECT_SEPARATOR).map(v => parseFloat(v) || 1);
                ratio = width / height;
            }
            else {
                ratio = parseFloat(aspect) || 1;
            }

            return `${100 / Math.abs(ratio)}%`;
        });

        const computedStyles = computed<any>(() => {
            return {
                paddingBottom: padding.value,
                height: 0
            };
        });

        return {
            computedStyles,
        }
    }
})
