import { computed, defineComponent, PropType } from 'vue'

export default defineComponent({
    name: "FsCardImg",
    props: {
        /**
        * Value to set for the `alt` attribute
        */
        alt: {
            type: String
        },
        /**
        * Set if the image will be placed at the bottom of the card
        */
        bottom: {
            type: Boolean,
            default: false
        },
        /**
        * Set if the image will be placed at the end (right) of the card
        */
        end: {
            type: Boolean,
            default: false
        },
        /**
        * The value to set on the image's 'height' attribute
        */
        height: {
            type: [Number, String] as PropType<Number | String>
        },
        /**
        * Set if the image will be placed at the start (left) of the card. Synonym for the 'left' prop
        */
        left: {
            type: Boolean,
            default: false
        },
        /**
        * Set if the image will be placed at the end (right) of the card. Synonym for the 'right' prop
        */
        right: {
            type: Boolean,
            default: false
        },
        /**
        * URL to set for the `src` attribute
        */
        src: {
            type: String
        },
        /**
        * Set if the image will be placed at the start (left) of the card
        */
        start: {
            type: Boolean,
            default: false
        },
        /**
        * Set if the image will be placed at the top of the card
        */
        top: {
            type: Boolean,
            default: false
        },
        /**
        * The value to set on the image's 'width' attribute
        */
        width: {
            type: [Number, String] as PropType<Number | String>
        }
    },
    setup(props){
        const getWidth = computed<number> (() => {
            return (typeof props.width === 'number' ? props.width : parseInt(props.width as string, 10)) ||
            undefined;
        });
   
       const getHeight = computed<number> (() => {
           return (typeof props.height === 'number' ? props.height : parseInt(props.height as string, 10)) ||
           undefined;
        });

        const finalClass = computed<any>(() => {
        return ({
            'float-left': props.left,
            'float-right': props.right,
            'card-img-top': props.top,
            'card-img-right': props.right || props.end,
            'card-img-bottom': props.bottom,
            'card-img-left': props.left || props.start,
            'card-img': !props.top && !props.right && !props.end && !props.bottom && !props.left && !props.start
            })     
        })

      return {
        finalClass,
        getWidth,
        getHeight
      };
    }
})