import { computed, defineComponent, PropType } from 'vue'

export default defineComponent({
     name: "FsDropdownGroup",
     props: {
	     /**
	     * The ID of the element that provides additional context for this component. Used as the value for the `aria-describedby` attribute
	     */
	     ariaDescribedby: {
	     	type: String
	     },
	     /**
	     * Text content to place in the header
	     */
	     header: {
	     	type: String
	     },
	     /**
	     * CSS class (or classes) to add to the header
	     */
	     headerClasses: {
	     	type: [Array, Object, String] as PropType<[] | Object | String>
	     },
	     /**
	     * Specify the HTML tag to render instead of the default tag for the header
	     */
	     headerTag: {
	     	type: String,
	     	default: 'header'
	     },
	     /**
	     * Applies one of the Bootstrap theme color variants to the header
	     */
	     headerVariant: {
	     	type: String
	     },
	     /**
	     * Used to set the `id` attribute on the rendered content, and used as the base to generate any additional element IDs as needed
	     */
	     id: {
	     	type: String
	     }
     },
	emits: [],
    setup(props){
     const headerId = computed<any>(() => {
          return  props.id ? [props.id, 'group_dd_header'].join('_') : undefined;
      });

     const headerRoll = computed<any>(() => {
          return props.headerTag === 'header' ? undefined : 'heading';
          
      });

     const finalHeaderClass = computed<any>(() => {
          return ({
               [`text-${props.headerVariant}`]: props.headerVariant,
          })
      });

      return {
          headerId,
          headerRoll,
          finalHeaderClass
      };
    }
})