import { defineComponent, provide, ref } from 'vue';
import FsProgressBar from './fsProgressBar.vue';

export default defineComponent ({
    name: "FsProgress",
    props: {
    /**
     * Enable the animated background. Also automatically sets 'striped'
     */
    animated: {
        type: Boolean,
        default: false
    },

    /**
     * Override the default height by specifying a CSS height value (including units)
     */
    height: {
        type: String,
    },

    /**
     * Set the maximum value
     */
    max: {
        type: [Number, String],
        default: 100
    },

    /**
     * The number of digits after the decimal to round the value to
     */
     precision: {
        type: [Number, String],
        default: 0
    },

    /**
     * Displays the current progress value as a percentage
     */
     showProgress: {
        type: Boolean,
        default: false
    },
    
    /**
     * Displays the current progress value
     */
     showValue: {
        type: Boolean,
        default: false
    },

    /**
     * Enable the striped background
     */
     striped: {
        type: Boolean,
        default: false
    },

    /**
     * The current value of the progress bar
     */
     value: {
        type: [Number, String],
        default: 0
    },

    /**
     * Applies one of the Bootstrap theme color variants to the component
     */
     variant: {
        type: String,
    },
},
components: {
    FsProgressBar
},

setup(props){
    provide('currentFsProgress', ref(props));
  },
});
