import { computed, defineComponent, PropType } from 'vue'

export default defineComponent({
  name: "FsCardHeader",
  props: {
    /**
    * Text content to place in the header
    */
    header: {
      type: String
    },
    /**
    * Applies one of the Bootstrap theme color variants to the header background
    */
    headerBgVariant: {
      type: String
    },
    /**
    * Applies one of the Bootstrap theme color variants to the header border
    */
    headerBorderVariant: {
      type: String
    },
    /**
    * CSS class (or classes) to apply to the header
    */
    headerClass: {
      type: [Array, Object, String] as PropType<[] | Object | String>
    },
    /**
    * HTML string content to place in the header
    */
    headerHtml: {
      type: String
    },
    /**
    * Specify the HTML tag to render instead of the default tag for the header
    */
    headerTag: {
      type: String,
      default: 'div'
    },
    /**
    * Applies one of the Bootstrap theme color variants to the header text
    */
    headerTextVariant: {
      type: String
    }
  },
  emits: ['click'],
  setup(props, { emit }) {
    const finalClass = computed<any>(() => {
      return ({
        ['text-' + props.headerTextVariant]: props.headerTextVariant,
        ['bg-' + props.headerBgVariant]: props.headerBgVariant,
        ['border-' + props.headerBorderVariant]: props.headerBorderVariant,
      })
    });
    function clicked(...args: any[]) {
      emit('click', args);
    }
    return {
      finalClass,
      clicked
    };
  }


})