import { defineComponent } from 'vue';
import FsNav from '../../fsNav/fsNav.vue';

export default defineComponent({
    name: "FsNavbarNav",
    props: {
        /**
        * Align the nav items in the nav: 'start' (or 'left'), 'center', 'end' (or 'right')
        */
        align: {
            type: String
        },
        /**
        * Proportionately fills all horizontal space with nav items. All horizontal space is occupied, but not every nav item has the same width
        */
        fill: {
            type: Boolean,
            default: false
        },
        /**
        * Fills all horizontal space with nav items, but unlike 'fill', every nav item will be the same width
        */
        justified: {
            type: Boolean,
            default: false
        },
        /**
        * Makes the nav smaller
        */
        small: {
            type: Boolean,
            default: false
        },
        /**
        * Specify the HTML tag to render instead of the default tag
        */
        tag: {
            type: String,
            default: 'ul'
        },
    },
    components: {
        FsNav
    },
    emits: [],
})
