import { computed, defineComponent, onBeforeMount, onMounted, Ref, ref, watch } from 'vue'
import generateId from '../../utils/generateId';
import { looseEqual } from '../../utils/loose-equal';

export default defineComponent({
    name: "FsFormCheckbox",
    model: {
        prop: 'checked',
        event: 'change'
    },
    props: {
        /**
        * Sets the value of `aria-label` attribute on the rendered element
        */
        ariaLabel: {
            type: String
        },
        /**
        * The ID of the element that provides a label for this component. Used as the value for the `aria-labelledby` attribute
        */
        ariaLabelledby: {
            type: String
        },
        /**
        * When set to `true`, attempts to auto-focus the control when it is mounted, or re-activated when in a keep-alive. Does not set the `autofocus` attribute on the control
        */
        autofocus: {
            type: Boolean,
            default: false
        },
        /**
        * When set, renders the checkbox with the appearance of a button
        */
        button: {
            type: Boolean,
            default: false
        },
        /**
        * Applies on of Bootstrap's theme colors when in 'button' mode
        */
        buttonVariant: {
            type: String,
            default: 'secondary'

        },
        /**
        * The current value of the checkbox(es). Must be an array when there are multiple checkboxes bound to the same v-model
        */
        checked: {                     
            default: null
        },
        /**
        * When set to `true`, disables the component's functionality and places it in a disabled state
        */
        disabled: {
            type: Boolean,
            default: false
        },
        /**
        * ID of the form that the form control belongs to. Sets the `form` attribute on the control
        */
        form: {
            type: String
        },
        /**
        * Used to set the `id` attribute on the rendered content, and used as the base to generate any additional element IDs as needed
        */
        id: {
            type: String
        },
        /**
        * Renders the checkbox in an indeterminate state. Two-way binding with v-model:indeterminate
        */
        indeterminate: {
            type: Boolean,
            default: false
        },
        /**
        * When set, renders the checkbox as an inline element rather than as a 100% width block
        */
        inline: {
            type: Boolean,
            default: false
        },
        /**
        * Sets the value of the `name` attribute on the form control
        */
        name: {
            type: String
        },
        /**
        * Render the form control in plain mode, rather than custom styled mode
        */
        plain: {
            type: Boolean,
            default: false
        },
        /**
        * Adds the `required` attribute to the form control
        */
        required: {
            type: Boolean,
            default: false
        },
        /**
        * Set the size of the component's appearance. 'sm', 'md' (default), or 'lg'
        */
        size: {
            type: String
        },
        /**
        * Controls the validation state appearance of the component. `true` for valid, `false` for invalid, or `null` for no validation state
        */
        state: {
            type: Boolean,
            default: null
        },
        /**
        * When set, renders the checkbox with the appearance of a switch
        */
        switch: {
            type: Boolean,
            default: false
        },
        /**
        * Value returned when this checkbox is unchecked. Note not applicable when multiple checkboxes bound to the same v-model array
        */
        uncheckedValue: {
            default: false
        },
        /**
        * Value returned when this checkbox is checked
        */
        value: {
            default: true
        },

        modelValue: undefined
    },
    emits: ['change', 'blur', 'focus', 'input','update:modelValue', 'click','update:indeterminate'],
    setup(props, { emit, slots }){
        const computedId: Ref<string> = ref("");
        const isFocused: Ref<boolean> = ref(false);
        const hasDefault = ref(false);
        const input = ref();

        onBeforeMount(() => {
            computedId.value = generateId(props.id?.toString(), 'form-check');
        })

        onMounted(() => {
            if (props.indeterminate) {
                setIndeterminate(props.indeterminate);
            }
        });

        function onClicked(event: Event): void {
            emit('click', event);
        }
 
        function onBlur(event: Event): void {
            isFocused.value = false;
            emit('blur', event)
        }
    
        function onFocus(event: Event): void {
            isFocused.value = true;
            emit('focus', event)
        }
        
        const computeRole = computed<any>(() => {
            return props.switch ? 'switch' : null;
        })
        
        const localValue = computed({
            get: (): unknown[] | Set<unknown> | boolean | undefined => {
              if (props.uncheckedValue) {
                if (!Array.isArray(props.modelValue)) {
                  return props.modelValue === props.value
                }
                return props.modelValue.indexOf(props.value) > -1
              }
              return props.modelValue as unknown[] | Set<unknown> | boolean | undefined
            },
            set: (newValue: any) => {
              let emitValue = newValue
              if (!Array.isArray(props.modelValue)) {
                emitValue = newValue ? props.value : props.uncheckedValue
              } else {
                if (props.uncheckedValue) {
                  emitValue = props.modelValue
                  if (newValue) {
                    if (emitValue.indexOf(props.uncheckedValue) > -1)
                      emitValue.splice(emitValue.indexOf(props.uncheckedValue), 1)
                    emitValue.push(props.value)
                  } else {
                    if (emitValue.indexOf(props.value) > -1)
                      emitValue.splice(emitValue.indexOf(props.value), 1)
                    emitValue.push(props.uncheckedValue)
                  }
                }
              }
              emit('update:modelValue', emitValue)
              emit('input', emitValue)
              emit('change', emitValue)
              // Indeterminate becomes false when clicked / changed
              emit('update:indeterminate', false)
            },
          });

        const wrapperClasses = computed<any>(() => {
            return ({
                'form-check': !props.button,
                'btn-group-toggle d-inline-block': props.button,
                'form-check-inline': props.inline,
                'form-switch': props.switch,
                [`form-control-${props.size}`]: props.size,
            })
        });

        const inputClasses = computed<any>(() => {
            return ({
                'form-check-input': true,
                'is-valid': props.state === true,
                'is-invalid': props.state === false,
                'btn-check': props.button,
            })
        });

        const labelClasses = computed<any>(() => {
            return ({
                'form-check-label': true,
                'btn': props.button,
                [`btn-${props.buttonVariant}`]: props.button,
                [`btn-${props.size}`]: props.button && props.size,
            })
        });

        if (slots.default && slots.default().length) {
            hasDefault.value = true;
        }

        watch(() =>
            props.indeterminate, (newValue, oldValue) => {
                if (!looseEqual(newValue, oldValue)) {
                    setIndeterminate(newValue)
                }
            });

        function setIndeterminate(state): void {
            // Indeterminate only supported in single checkbox mode
            if (Array.isArray(props.modelValue)) {
                state = false
            }
            if (input?.value) {
                input.value.indeterminate = state
            }
        }
        
        return {
            computedId,
            isFocused,
            localValue,
            onBlur,
            onFocus,
            onClicked,
            wrapperClasses,
            inputClasses,
            labelClasses,
            hasDefault,
            computeRole,
            input
        };
    }
})